import { Injectable } from "@angular/core";
import { Observable } from "rxjs/Observable";
import {HttpWrapper} from "../util/http-wrapper.util";
import {TaskAddModel} from "../models/TaskAddModel";
import {ResponseData} from "../models/response-data";


@Injectable()
export class TaskService {

    constructor(private http: HttpWrapper) {
    }

    getStation(): Observable<any> {
        return this.http.post("/api/no-auth/station/getAllStation", null);
    }

    /**
     * 通过服务端请求获得文件的真实路径
     * @param fileId
     */
    getFileRealPath(fileId: string): Observable<string> {
        const URL = '/api/no-auth/path';
        console.log(fileId);
        return this.http.post(URL, fileId, {showLoading: false, showError: false});

    };

    /**
     * 通过服务端请求获得任务列表
     * @param fileId
     */
    getTaskList(param: any): Observable<any> {
        const URL = '/task/getTaskList';
        console.log(param);
        return this.http.post(URL, param, {showLoading: false, showError: true});
    };

    /**
     * 通过服务端请求获得任务列表【根据条件查询】
     * @param fileId
     */
    getTaskForFilter(param: any): Observable<any> {
        const URL = '/task/getTaskForFilter';
        console.log(param);
        return this.http.post(URL, param, {showLoading: false, showError: true});
    };

    /**
     * 接取任务
     * @param fileId
     */
    acceptTask(id: string): Observable<any> {
        const URL = '/task/acceptTask';
        console.log(id);
        return this.http.post(URL, id, {showLoading: false, showError: true});
    };

    /**
     * 接取任务(日常)
     * @param fileId
     */
    chAcceptTask(id: string): Observable<any> {
        const URL = '/chtask/acceptTask';
        console.log(id);
        return this.http.post(URL, {taskId: id}, {showLoading: false, showError: true});
    };

    /**
     * 发布任务
     * @param fileId
     */
    releaseTask(data: any): Observable<any> {
        const URL = '/task/releaseTask';
        console.log(data);
        return this.http.post(URL, data, {showLoading: false, showError: true});
    };

    /**
     * 查询已接任务
     * @param fileId
     */
    getAlreadyAccept(data: any): Observable<any> {
        const URL = '/task/getAlreadyRelease';
        console.log(data);
        return this.http.post(URL, data, {showLoading: false, showError: true});
    };

    /**
     * 查询自己已经发布的任务
     * @param fileId
     */
    getHistoryReleaseTask(data: any): Observable<any> {
        const URL = '/task/getHistoryReleaseTask';
        console.log(data);
        return this.http.post(URL, data, {showError: true});
    };

    /**
     * 根据城市获取区县信息
     * @param fileId
     */
    getArea(data: any): Observable<any> {
        const URL = '/task/getArea';
        console.log(data);
        return this.http.post(URL, data, {showLoading: false, showError: true});
    };

    /**
     * 获取当前登录人的已完成的任务
     * @param fileId
     */
    getAlreadyFinish(data: any): Observable<any> {
        const URL = '/task/getAlreadyFinish';
        console.log(data);
        return this.http.post(URL, data, {showLoading: false, showError: true});
    };

    /**
     * 上传文件
     * @param fileId
     */
    uploadFile(data: any): Observable<any> {
        const URL = '/file/upload';
        console.log(data);
        return this.http.post(URL, data, {showLoading: false, showError: true});
    };

    /**
     * 完成任务
     * @param fileId
     */
    finishTask(data: any): Observable<any> {
        const URL = '/task/finishTask';
        console.log(data);
        return this.http.post(URL, data, {showLoading: false, showError: true});
    };

    /**
     * 统计查询任务数量
     * @param fileId
     */
    getTaskCount(): Observable<any> {
        const URL = '/task/getTaskCount';
        return this.http.post(URL, "", {showLoading: false, showError: true});
    };


    /**
     * 发布者确认任务，并写入任务评价，之后重发或者删除任务
     * @param fileId
     */
    confirmForPublished(data:any): Observable<any> {
        const URL = '/task/confirmForPublished';
        return this.http.post(URL, data, {showLoading: false, showError: true});
    };

    /**
     * 保存当前操作人身份证图片id到数据库
     * @param fileId
     */
    updateCardImg(data:any): Observable<any> {
        const URL = '/task/updateCardImg';
        return this.http.post(URL, data, {showLoading: false, showError: true});
    };

    /**
     * 保存当前操作人位置信息
     * @param fileId
     */
    updatePosition(data:any): Observable<any> {
        const URL = '/task/updatePosition';
        return this.http.post(URL, data, {showLoading: false, showError: true});
    };

    /**
     * 任务状态变更
     * @param fileId
     */
    updateState(data:any): Observable<any> {
        const URL = '/task/updateState';
        return this.http.post(URL, data, {showLoading: false, showError: true});
    };

    /**
     * 保存图片
     * @param fileId
     */
    saveImg(data:any): Observable<any> {
        const URL = '/task/saveImg';
        return this.http.post(URL, data, {showLoading: false, showError: true});
    };

    /**
     * 添加评价
     * @param fileId
     */
    addEval(data:any): Observable<any> {
        const URL = '/task/addEval';
        return this.http.post(URL, data, {showLoading: false, showError: true});
    };

    /**
     * 查询任务进程信息
     * @param fileId
     */
    queryTaskProgressState(data:any): Observable<any> {
        const URL = '/task/queryTaskProgressState';
        return this.http.post(URL, data, {showLoading: false, showError: true});
    };

    /**
     * 发布者取消任务
     * @param fileId
     */
    cancelTask(data:any): Observable<any> {
        const URL = '/task/cancelTask';
        return this.http.post(URL, data, {showLoading: false, showError: true});
    };

    /**
     * 发布者开始任务（日常）
     * @param fileId
     */
    startTask(data:any): Observable<any> {
        const URL = '/chtask/startTask';
        return this.http.post(URL, data, {showLoading: false, showError: true});
    };

    /**
     * 任务超时处理
     * @param fileId
     */
    timeoutHandler(data:any): Observable<any> {
        const URL = '/task/timeoutHandler';
        return this.http.post(URL, data, {showLoading: false, showError: true});
    };

    addTask(data: TaskAddModel): Observable<any> {
        const URL = '/chtask/addTask';
        return this.http.post(URL, data, {showLoading: false}).map((response: ResponseData) => {
            if (response.success) {
                return response.data;
            } else {
                throw response;
            }
        });
    }

    /**
     * 退出任务
     * @param {string} taskId
     * @returns {Observable<any>}
     */
    exitTask(taskId: string): Observable<any> {
        const URL = '/chtask/exitTask';
        return this.http.post(URL, taskId);
    }

    /**
     * 回答问题
     * @param {string} taskId
     * @returns {Observable<any>}
     */
    answerTask(data: {taskId: string, answer: string}): Observable<any> {
        const URL = '/chtask/answerTask';
        return this.http.post(URL, data);
    }

    /**
     * 结束任务，获取数据库时间值
     * @param {string} taskId
     * @returns {Observable<any>}
     */
    endTask(data: {taskId: string}): Observable<any> {
        const URL = '/chtask/endTask';
        return this.http.post(URL, data);
    }

    /**
     * 确认完成
     * @param {string} taskId
     * @returns {Observable<any>}
     */
    confirmFinishTask(data: {taskId: string}): Observable<any> {
        const URL = '/chtask/confirmFinishTask';
        return this.http.post(URL, data);
    }

    /**
     * 确认未完成
     * @param {string} taskId
     * @returns {Observable<any>}
     */
    confirmUnFinishTask(data: {taskId: string}): Observable<any> {
        const URL = '/chtask/confirmUnFinishTask';
        return this.http.post(URL, data);
    }


}



