import { Component, ViewChild } from '@angular/core';
import { NavController, NavParams, Navbar } from 'ionic-angular';
import { TaskService} from "../../../providers/task.service";
import {CommonUtil} from "../../../util/model/common.util";
import {HistoryDetailPage} from "../detail/history_detail";
import {ReleaseMapPage} from "../map/release_map";
import {HomeService} from "../../../providers/home.service";
import {DbDict} from "../../../util/DbDict";
import { HomePage } from '../../home/home';
import {ConfirmPublisherCheckPage} from "../confirm/confirm_publisher_check";

@Component({
    selector: 'page-re-history',
    templateUrl: 'release_history.html'
})
export class ReleaseHistoryPage {
    @ViewChild(Navbar) navBar: Navbar;
    picId: string;
    level: any;//任务等级
    public taskList: Array<any>;
    progressInfo:any={};//任务进程信息
    types = [
        {id: "1", title: "待接", states: "'01'"},
        {id: "2", title: "进行中", states: "'02','03'"},
        {id: "3", title: "完成", states: "'04'"},
        {id: "4", title: "回收站", states: "'10'"}
    ];
    selectType = this.types[0];//缓存类型信息

    constructor(public navCtrl: NavController,
                private param:NavParams,
                private homeService: HomeService,
                private taskService: TaskService
            ) {
        this.initHistoryReleaseTask();//初始化查询自己已发布的任务
        
    }
    backButtonClick(){
        this.navCtrl.setRoot(HomePage);
    }
    showDetail(task){//展示任务明细
        this.navCtrl.push(HistoryDetailPage,{task: task});
    }
    publisherCheckTask(task){
        this.navCtrl.push(ConfirmPublisherCheckPage,{task: task});
    }
    ionViewDidLoad(){
        this.navBar.backButtonClick = this.backButtonClick;
    }
    ngOnInit(): void {
        this.homeService.loadScript()
            .subscribe(() => {
                this.initHistoryReleaseTask();//初始化查询自己已发布的任务
            });
    }

    //展示地图
    //查询任务进程状态
    showMap(id:any){
        let param:any={};
        param.id = id;
        this.taskService.queryTaskProgressState(param).subscribe(data => {
            if(data.length > 0){
                for(let i = 0;i < data.length;i++){
                    let item = data[i];
                    item.STATE_STR = "";
                    if(item.STATE == "02"){item.STATE_STR = "镖师接镖";}
                    if(item.STATE == "03"){item.STATE_STR = "镖师取镖";}
                    if(item.STATE == "04"){item.STATE_STR = "押镖完成";}
                }
                this.progressInfo = data;
                for (let task of this.taskList) {
                    if(id == task.ID){
                        this.navCtrl.push(ReleaseMapPage,{task: task,progressInfo:this.progressInfo});
                    }
                }
            }
        }, CommonUtil.emptyCallback);
    }

    //查询自己已发布的任务【01  是待接取】
    initHistoryReleaseTask(){
        this.taskService.getHistoryReleaseTask("01").subscribe(data => {
            this.taskList = data;
            for(let i=0;i < data.length;i++){
                //标准代码转中文描述
                this.taskList[i].BIG_TYPE_STR = DbDict.code2type(this.taskList[i].BIG_TYPE, "TASK_BIG_TYPE");
            }
        }, CommonUtil.emptyCallback);
    }

    //根据类型查询已发布的任务
    queryHistoryReleaseTask(type:any){
        this.selectType = type;
        this.taskService.getHistoryReleaseTask(type.states).subscribe(data => {
            this.taskList = data;
            for(let i=0;i < data.length;i++){
                //标准代码转中文描述
                this.taskList[i].BIG_TYPE_STR = DbDict.code2type(this.taskList[i].BIG_TYPE, "TASK_BIG_TYPE");
            }
        }, CommonUtil.emptyCallback);
    }
}



