import { ComponentsUtil } from './../../../util/components.util';
import { ChargePage } from './../../charge/charge';
import { Component } from '@angular/core';
import {ActionSheetController, AlertController, NavController, NavParams, List, ModalController} from 'ionic-angular';
import { TaskService} from "../../../providers/task.service";
import {CommonUtil} from "../../../util/model/common.util";
import {ReleaseHistoryPage} from "../taskList/release_history";
import {Camera, CameraOptions} from "@ionic-native/camera";
import {ImagePicker} from "@ionic-native/image-picker";
import { PicturesPage } from '../../modal/pictures/pictures';

@Component({
    selector: 'page-release4',
    templateUrl: 'release_4.html'
})
export class ReleasePage_4 {
    detail: string;//任务详细
    coin: string;//奖励金额
    time: string;//任务要求时间
    remark : string;//备注
    taskInfo: any = {};//任务
    areaData:any;//地址
    date:any;
    range:any = {};//金币范围
    level:any;

    consume_time:any;//耗时
    pick_time:any;//取件时间
    limit_time:any;
    weight:any;//物品重量
    value:any;//物品价值
    PUBLISH_IMG_ID:any="";//发布图片id
    imgIdsStrList:any=[];
    imgIdsStr:string;
    path:any;//base64 文件路径
    fileInfo:any;//上传的文件信息
    imgId:any;//上传的文件id
    path1:string;
    path2:string;
    path3:string;
    path4:string;
    path5:string;
    checkPickTime:any;


    constructor(public navCtrl: NavController,
                private param: NavParams,
                public modalController:ModalController,
                public alertCtrl: AlertController,
                public actionSheetCtrl: ActionSheetController,
                public imagePicker: ImagePicker,
                public camera: Camera,
                private taskService: TaskService,
            private cmpUtil:ComponentsUtil) {
        this.taskInfo  = param.get("taskInfo");
        //this.areaData  = param.get("areaData");
        //this.taskInfo.area = this.areaData;

        //设置默认值
        this.level = "5";
        this.coin = "50000";
        this.range.min = "50000";
        this.range.max = "99999";
        this.date = this.getDate();//获取当前时间
        this.path1 = "./assets/imgs/star2.png";
        this.path2 = "./assets/imgs/star2.png";
        this.path3 = "./assets/imgs/star2.png";
        this.path4 = "./assets/imgs/star2.png";
        this.path5 = "./assets/imgs/star2.png";
        this.checkPickTime="0";
        this.pick_time=new Date(+new Date()+8*3600*1000).toISOString();
        // let year = this.pick_time.getFullYear();
        //       let  month = this.pick_time.getMonth()+1;
        //       let date = this.pick_time.getDate();
        //       if(month<10){
        //         month="0"+month;
        //       }
        //       if(date<10){
        //         date="0"+month;
        //       }
        //       let hour = this.pick_time.getHours();
        //       let minute  = this.pick_time.getMinutes();
        //       let second = this.pick_time.getSeconds();
        this.limit_time=new Date(+new Date()+8*3600*1000).toISOString();
        console.log(this.pick_time);
        console.log(this.limit_time);
    }
    releaseTask(){//发布任务
        let consumeTimeStr:any;//消耗时间
        let pickTimeStr:any=this.pick_time.replace(/T/g,' ').replace(/\.[\d]{3}Z/,'');
        if(this.taskInfo.type_2_code == "11"){//镖局 获取消耗时间
            if(document.querySelectorAll("div[class='datetime-text']")&&document.querySelectorAll("div[class='datetime-text']").length>0){
                consumeTimeStr = document.querySelectorAll("div[class='datetime-text']")[0].innerHTML;
                
            }
           
        }
        var x = consumeTimeStr.indexOf("分");
        if(x==-1){
            this.consume_time = consumeTimeStr;
        }else{
            consumeTimeStr = consumeTimeStr.substring(0,x);//22 分钟内
            this.consume_time = consumeTimeStr;
        }
        /*--------------------------【镖局任务信息校验 start】-----------------------------*/
        if(this.consume_time == "" || this.consume_time == null){
            this.showAlert('请填写任务耗时');
            return;
        }
        if((this.pick_time == "" || this.pick_time == null)&&this.checkPickTime==1){
            this.showAlert('请填写取件时间');
            return;
        }
        console.log("pick_time:"+pickTimeStr);
        console.log("consume_time:"+this.consume_time);
        
        if(this.weight == "" || this.weight == null){
            this.showAlert('请填物品重量');
            return;
        }
        if(this.value == "" || this.value == null){
            this.showAlert('请填物品价值');
            return;
        }
        // if(this.PUBLISH_IMG_ID == "" || this.PUBLISH_IMG_ID == null){
        //     this.showAlert('为了保障您的利益，请对物品进行拍照');
        //     return;
        // }

        /*--------------------------【镖局任务信息校验 end】-----------------------------*/
        this.taskInfo.consume_time = this.consume_time;
        this.taskInfo.pick_time = this.checkPickTime=='0'?null:pickTimeStr;
        this.taskInfo.weight = this.weight;
        this.taskInfo.value = this.value;
        this.taskInfo.coin = this.coin;
        this.taskInfo.level = this.level;
        this.taskInfo.PUBLISH_IMG_ID=this.PUBLISH_IMG_ID;
        this.cmpUtil.showLoading("发布中...");
        this.taskService.releaseTask(this.taskInfo).subscribe(data => {
            let that=this;
            //发布完成任务自动跳转到历史发布界面
            if(data){
                this.cmpUtil.showAlert("恭喜您，任务发布成功");
                this.navCtrl.push(ReleaseHistoryPage);
                this.cmpUtil.hideLoading();
            }else{
                this.alertCtrl.create({
                    title: "提示",
                    message: "任务扣除后不足500金币,是否到充值页面?",
                    buttons: [
                        {
                            text: '取消',
                            role: 'cancel',
                            handler: function () {
                                console.log('选择取消按钮');
                                that.cmpUtil.hideLoading();
                            }
                        }, {
                            text:  '确认',
                            handler:  function () {
                                that.cmpUtil.hideLoading();
                                that.navCtrl.push(ChargePage);
                            }
                        }
                    ]
                }).present();
            }
        }, CommonUtil.emptyCallback);
    }

    //获取当前时间
    getDate() {
        let date = new Date();
        let y:any = date.getFullYear();
        let m:any = date.getMonth() + 1;
        m = m < 10 ? ('0' + m) : m;
        let d:any = date.getDate();
        d = d < 10 ? ('0' + d) : d;
        return y + '-' + m + '-' + d;
    }

    //设置任务等级
    setLevel(num){
        this.initStar();//初始化星星
        for(var i = parseInt(num);i >0;i--){
            (<HTMLImageElement>document.getElementById("star_"+i)).src="./assets/imgs/star2.png";
        }
        this.level = num+"";//保存等级
        this.taskInfo.level = this.level;
        this.setRange(num);
    }

    //设置根据任务等级金币限制
    setRange(num){
        if(num =='1'){
            this.range.min = "0";
            this.range.max = "2000";
            this.coin = "2000";
        }
        if(num =='2'){
            this.range.min = "2000";
            this.range.max = "4999";
            this.coin = "4999";
        }
        if(num =='3'){
            this.range.min = "5000";
            this.range.max = "9999";
            this.coin = "9999";
        }
        if(num =='4'){
            this.range.min = "10000";
            this.range.max = "49999";
            this.coin = "49999";
        }
        if(num =='5'){
            this.range.min = "50000";
            this.range.max = "99999";
            this.coin = "99999";
        }
    }

    //初始化星星，全部设置为空心
    initStar(){
        (<HTMLImageElement>document.getElementById("star_1")).src ="./assets/imgs/star1.png";
        (<HTMLImageElement>document.getElementById("star_2")).src ="./assets/imgs/star1.png";
        (<HTMLImageElement>document.getElementById("star_3")).src ="./assets/imgs/star1.png";
        (<HTMLImageElement>document.getElementById("star_4")).src ="./assets/imgs/star1.png";
        (<HTMLImageElement>document.getElementById("star_5")).src ="./assets/imgs/star1.png";
    }

    onInputTime(){
        if(parseInt(this.coin) >=50000){
            this.path5 = "./assets/imgs/star2.png";
            this.path4 = "./assets/imgs/star2.png";
            this.path3 = "./assets/imgs/star2.png";
            this.path2 = "./assets/imgs/star2.png";
            this.path1 = "./assets/imgs/star2.png";

            return;
        }
        if(parseInt(this.coin) >=10000){
            this.path5 = "./assets/imgs/star1.png";
            this.path4 = "./assets/imgs/star2.png";
            this.path3 = "./assets/imgs/star2.png";
            this.path2 = "./assets/imgs/star2.png";
            this.path1 = "./assets/imgs/star2.png";

            return;
        }
        if(parseInt(this.coin) >=5000){
            this.path5 = "./assets/imgs/star1.png";
            this.path4 = "./assets/imgs/star1.png";
            this.path3 = "./assets/imgs/star2.png";
            this.path2 = "./assets/imgs/star2.png";
            this.path1 = "./assets/imgs/star2.png";

            return;
        }
        if(parseInt(this.coin) >=2000){
            this.path5 = "./assets/imgs/star1.png";
            this.path4 = "./assets/imgs/star1.png";
            this.path3 = "./assets/imgs/star1.png";
            this.path2 = "./assets/imgs/star2.png";
            this.path1 = "./assets/imgs/star2.png";

            return;
        }
        if(parseInt(this.coin) <2000){
            this.path5 = "./assets/imgs/star1.png";
            this.path4 = "./assets/imgs/star1.png";
            this.path3 = "./assets/imgs/star1.png";
            this.path2 = "./assets/imgs/star1.png";
            this.path1 = "./assets/imgs/star2.png";

            return;
        }

    }


    //显示提示信息
    showAlert(str:string) {
        let alert = this.alertCtrl.create({
            title: '',
            subTitle: str
        });
        alert.present();
    }

    presentActionSheet() {
        let actionSheet = this.actionSheetCtrl.create({
            buttons: [{
                text: '拍照',
                role: 'takePhoto',
                handler: () => {
                    this.takePhoto();
                }
            },
            // {
            //     text: '从相册选择',
            //     role: 'chooseFromAlbum',
            //     handler: () => {
            //         this.chooseFromAlbum();
            //     }
            // },
             {
                text: '取消',
                role: 'cancel',
                handler: () => {
                    console.log("cancel");
                }
            }]
        });

        actionSheet.present().then(value => {
            return value;
        });
    }
    takePhoto() {
        const options: CameraOptions = {
            quality: 100,
            allowEdit: true,
            targetWidth: 400,
            targetHeight: 400,
            saveToPhotoAlbum: true,
            sourceType:this.camera.PictureSourceType.CAMERA,//从相机选择图片
            destinationType: this.camera.DestinationType.DATA_URL
        };

        this.camera.getPicture(options).then(imageData => {
            let base64Image = {'fileType':'jpg','fileBase64':imageData};
            this.path = base64Image;
            this.upload();
            console.log('base64Image: ' + base64Image);
        }, error => {
            console.log('Error: ' + error);
        });
    }

    chooseFromAlbum() {
        const options: CameraOptions = {
            quality: 100,
            allowEdit: true,
            targetWidth: 400,
            targetHeight: 400,
            saveToPhotoAlbum: true,
            sourceType:this.camera.PictureSourceType.SAVEDPHOTOALBUM,//从相册选择图片
            destinationType: this.camera.DestinationType.DATA_URL
        };
        console.log("进入相册")
        this.camera.getPicture(options).then(imageData => {
            console.log("选择图片");

            let base64Image = {'fileType':'jpg','fileBase64':imageData};
            this.path = base64Image;
            

            this.upload();
            console.log('base64Image: ' + base64Image);
        }, error => {
            console.log('Error: ' + error);
        });
    }

    presentAlert() {
        let alert = this.alertCtrl.create({title: "上传失败", message: "只能选择一张图片", buttons: ["确定"]});
        alert.present().then(value => {
            return value;
        });
    }

    upload(){
        console.log("上传图片");
        if(this.imgIdsStrList.length==4){
            this.alertCtrl.create({title: "", message: "最多上传四张图片", buttons: ["确定"]}).present();
            return;
        }
        this.taskService.uploadFile(this.path).subscribe(data => {
            console.log("上传返回"+data);
            console.log("上传返回ID"+data.fileId);
            this.fileInfo = data;
            //this.PUBLISH_IMG_ID = data.fileId;
            this.imgIdsStrList.push(data.fileId);
            this.imgIdsStr="";
            for(let i=0;i<this.imgIdsStrList.length;i++){
                if(i==0){
                    this.imgIdsStr=this.imgIdsStrList[i];
                }else{
                    this.imgIdsStr+=","+this.imgIdsStrList[i];
                }
                
            }
            this.PUBLISH_IMG_ID=this.imgIdsStr;

            console.log("上传返回ID"+this.PUBLISH_IMG_ID);
            console.log(this.PUBLISH_IMG_ID.split(',')[0]);

        }, CommonUtil.emptyCallback);
    }
    goPicturesPage(){
        if(this.imgIdsStr==""||this.imgIdsStr){
            console.log("图片没有,不跳转");
            return;

        }
       let picturespage= this.modalController.create(PicturesPage,{"imgIdsStr":this.imgIdsStr});
       picturespage.onDidDismiss(result=>{
            this.imgIdsStr=result.imgIdsStr;
        });
       picturespage.present();
    }

}



