import { FriendService } from './../../../providers/friend.service';
import {Component, OnDestroy, OnInit} from '@angular/core';
import {ActionSheetController, AlertController, NavController, NavParams} from 'ionic-angular';
import {HomeService} from "../../../providers/home.service";
import {Geolocation} from "@ionic-native/geolocation";
import "rxjs/add/operator/filter";
import "rxjs/add/operator/takeWhile";
import {ConfirmOrderPage} from "../confirm/confirm_order";
import {HomePage} from "../../home/home";
import {CommonUtil} from "../../../util/model/common.util";
import {TaskService} from "../../../providers/task.service";
import { CallNumber } from '@ionic-native/call-number';
import {Camera, CameraOptions} from "@ionic-native/camera";

declare let BMap: any;
declare let baidumap_location;

@Component({
    selector: 'page-order_map',
    templateUrl: 'order_map.html'
})
export class OrderMapPage implements OnInit, OnDestroy {
    alive: boolean;
    stations: Array<any>;
    picId: string;
    // 地图开启标志
    mapOn = true;
    // 搜索关键字
    searchKey = '';
    // 地图对象
    map: any;
    // 当前位置对象
    locationMarker: any;
    // 当前位置
    initLocate :any;
    task:any={};

    centerPoint:any;//地图中心点坐标
    centerAddr:any;//地图中心点详细地址
    distance:any=0;//距离
    path:any;

    constructor(public navCtrl: NavController,
                private param: NavParams,
                private geolocation: Geolocation,
                public actionSheetCtrl: ActionSheetController,
                public camera: Camera,
                public alertCtrl: AlertController,
                private taskService: TaskService,
                private homeService: HomeService,private callNumber:CallNumber,private friendService:FriendService) {
        this.task = param.get("task");
        this.centerPoint = "";
        this.centerAddr = "";
        //任务状态
        this.queryState();
        // this.address = '郑州火车站';
    }

    ngOnInit(): void {
        this.homeService.loadScript()
            .subscribe(() => {
                // 定位
                this.locating();

            });
    }

    // 页面控件初始化完成，加载数据
    ionViewWillEnter(): void {
        this.alive = true;
    }

    ionViewDidLeave(): void {
        this.alive = false;
    }

    // 页面销毁
    ngOnDestroy(): void {
        this.alive = false;
    }


    private locating(): void {
        // 百度地图API功能
        this.map = new BMap.Map("bmap");
        var point = new BMap.Point(116.331398,39.897445);
        this.map.centerAndZoom(point,12);
        var geolocation = new BMap.Geolocation();

        this.addIcon();//添加起点图片和终点图片
        let that:any = this;
        //用插件定位当前位置，添加图片
        baidumap_location.getCurrentPosition((result) => {
            let point = new BMap.Point(result.lontitude, result.latitude);
            let manIcon = new BMap.Icon("./assets/imgs/man.png", new BMap.Size(100, 100));
            let mk = new BMap.Marker(point, {icon: manIcon});
            that.map.addOverlay(mk);
            that.map.panTo(point);
        });

        //百度自带定位，不准
        // geolocation.getCurrentPosition(function(r){
        //     if(this.getStatus() == "0"){
        //         let manIcon = new BMap.Icon("./assets/imgs/man.png", new BMap.Size(100, 100));
        //         let mk = new BMap.Marker(r.point, {icon: manIcon});
        //         that.map.addOverlay(mk);
        //         that.map.panTo(r.point);
        //     }
        //     else {
        //     }
        // },{enableHighAccuracy: true})
    }

    //计算距离
    calDistance(){
        let myGeo = new BMap.Geocoder();
        let that = this;
        myGeo.getPoint(this.task.TO_ADDR, function(point){
            if (point) {
                baidumap_location.getCurrentPosition((result) => {
                    let _p = new BMap.Point(result.lontitude, result.latitude);
                    that.distance = that.map.getDistance(_p,point).toFixed(2);//距离 米
                    that.distance = that.distance/1000 ;//转换为公里
                });
            }else{
                //alert("您选择地址没有解析到结果!");
            }
        }, "北京市");

    }

    addIcon() {//添加起始位置图标
        let startIcon = new BMap.Icon("./assets/imgs/start.png", new BMap.Size(100, 100));
        let endIcon = new BMap.Icon("./assets/imgs/end.png", new BMap.Size(100, 100));

        //let marker1 = new BMap.Marker(point, {icon: myIcon});  // 创建标注
        //map.centerAndZoom(point, 16);

        let myGeo = new BMap.Geocoder();
        let that = this;
        // 将地址解析结果显示在地图上,并调整地图视野
        //若经纬度不为空按经纬度定位
        if(this.task.FROM_POINT != null && this.task.FROM_POINT != ""){
            let _str = this.task.FROM_POINT.split(",");//拆分字符串  经度,维度
            if(_str.length > 1) {
                let _point = new BMap.Point(parseFloat(_str[0]), parseFloat(_str[1]));
                let marker1 = new BMap.Marker(_point, {icon: startIcon});
                that.map.addOverlay(marker1);
            }
        }else{
            myGeo.getPoint(this.task.FROM_ADDR, function(point){
                if (point) {
                    let marker1 = new BMap.Marker(point, {icon: startIcon});
                    that.map.addOverlay(marker1);
                }else{
                    //alert("您选择地址没有解析到结果!");
                }
            }, "北京市");
        }

        // 将地址解析结果显示在地图上,并调整地图视野
        //若经纬度不为空按经纬度定位
        if(this.task.TO_POINT != null && this.task.TO_POINT != ""){
            let _str = this.task.TO_POINT.split(",");//拆分字符串  经度,维度
            if(_str.length > 1){
                let _point = new BMap.Point(parseFloat(_str[0]),parseFloat(_str[1]));
                let marker1 = new BMap.Marker(_point, {icon: endIcon});
                that.map.addOverlay(marker1);
            }
        }else {
            myGeo.getPoint(this.task.TO_ADDR, function (point) {
                if (point) {
                    let marker2 = new BMap.Marker(point, {icon: endIcon});
                    that.map.addOverlay(marker2);
                    that.map.centerAndZoom(point, 13);
                } else {
                    //alert("您选择地址没有解析到结果!");
                }
            }, "北京市");
        }
    }

    //更新任务状态
    updateState(code,detail){
        let progressInfo:any = {};
        progressInfo.progress_state = code;
        progressInfo.progress_description = detail;
        progressInfo.id = this.task.ID;
        this.taskService.updateState(progressInfo).subscribe(data => {
            if(code == "04"){//镖师点击完成任务，执行任务完成方法
                this.taskService.finishTask(this.task).subscribe(data => {

                }, CommonUtil.emptyCallback);
            }
            if(code == "05"){//点击任务完成时跳转到镖师拍照确认页面
                this.navCtrl.push(ConfirmOrderPage,{task:this.task});
            }
            this.queryState();//重新查询状态
        }, CommonUtil.emptyCallback);
    }

    //查询任务进程状态
    queryState(){
        let param:any={};
        param.id = this.task.ID;
        this.taskService.queryTaskProgressState(param).subscribe(data => {
            if(data.length > 0){//点击任务完成时跳转到镖师拍照确认页面
                this.task.pro_state = data[0].STATE;
            }
        }, CommonUtil.emptyCallback);


    }


    toHome(){//跳转到主页
        this.navCtrl.push(HomePage);
    }

    //显示提示信息
    showAlert(str:string) {
        let alert = this.alertCtrl.create({
            title: '',
            subTitle: str
        });
        alert.present();
    }
    goAddFriend(){
        this.friendService.addFriend(this.task.USER_ID).subscribe(data => {
            if(data){
                this.alertCtrl.create({
                    title: '',
                    subTitle: "添加成功"
                }).present();
            }else{
                this.alertCtrl.create({
                    title: '',
                    subTitle: "添加失败"
                }).present();
            }
        });
    }
    goCall(){
        let that=this;
         this.alertCtrl.create({
            title: "拨打:"+that.task.FROM_TELEPHONE+"?",
            message: "",
            buttons: [
                {
                    text: '取消',
                    role: 'cancel',
                    handler: function () {
                        console.log('选择取消按钮');
                    }
                }, {
                    text:  '确认',
                    handler:  function () {
                        that.callNumber.callNumber(that.task.FROM_TELEPHONE, true)
                            .then(res => console.log('Launched dialer!', res))
                            .catch(err => console.log('Error launching dialer', err));
                    }
                }
            ]
        }).present();
    }

    presentActionSheet() {
        let actionSheet = this.actionSheetCtrl.create({
            buttons: [{
                text: '拍照',
                role: 'takePhoto',
                handler: () => {
                    this.takePhoto();
                }
            },
                {
                    text: '取消',
                    role: 'cancel',
                    handler: () => {
                        console.log("cancel");
                    }
                }]
        });

        actionSheet.present().then(value => {
            return value;
        });
    }
    takePhoto() {
        const options: CameraOptions = {
            quality: 100,
            allowEdit: true,
            targetWidth: 400,
            targetHeight: 400,
            saveToPhotoAlbum: true,
            sourceType:this.camera.PictureSourceType.CAMERA,//从相机选择图片
            destinationType: this.camera.DestinationType.DATA_URL
        };

        this.camera.getPicture(options).then(imageData => {
            let base64Image = 'data:image/jpeg;base64,' + imageData;
            this.path = base64Image;
            console.log('base64Image: ' + base64Image);
        }, error => {
            console.log('Error: ' + error);
        });
    }
}



