import { MyService } from './../../../providers/my.service';
import {Component} from '@angular/core';
import { AlertController, NavController, NavParams, ViewController } from 'ionic-angular';
import {HomeService} from "../../../providers/home.service";
import {Geolocation} from "@ionic-native/geolocation";
import "rxjs/add/operator/filter";
import "rxjs/add/operator/takeWhile";
import { Contacts } from '@ionic-native/contacts';
import { GlobalVariable } from './../../../app/app.global';

declare let BMap: any;

@Component({
    selector: 'page-get_addr',
    templateUrl: 'get_addr.html'
})
export class getAddrPage  {
    id:any="";
    contact:any;
    telephone:any;
    address:any;
    stations: Array<any>;
    picId: string;
    // 地图开启标志
    mapOn = true;
    // 搜索关键字
    searchKey = '';
    // 地图对象
    map: any;
    // 当前位置对象
    locationMarker: any;
    // 当前位置
    initLocate :any;
    myValue:any;
    ac:any;

    centerPoint:any;//地图中心点坐标
    centerAddr:any;//地图中心点详细地址
    centerAddrFlag:boolean;
    cardAddr:any="";//门牌号
    signFlag:any=false;
    city:any = GlobalVariable.getCity();//当前定位的城市
    localSearch:any = {};
    searchCache:any="";

    constructor(public navCtrl: NavController,
                private param: NavParams,
                private geolocation: Geolocation,
                public alertCtrl: AlertController,
                private homeService: HomeService,
                public myService:MyService,
                public viewCtrl:ViewController,
                private contacts: Contacts) {
        this.centerPoint = "";
        this.centerAddr = "";
        this.id=param.data.ID?param.data.ID:"";
        this.contact=param.data.CONTACT?param.data.CONTACT:"";
        this.telephone=param.data.TELEPHONE?param.data.TELEPHONE:"";
        this.address=param.data.ADDRESS?param.data.ADDRESS:"";
        this.centerAddrFlag=false;
        // this.address = '郑州火车站';
    }

    ngOnInit(): void {
            this.homeService.loadScript()
            .subscribe(() => {
                // 定位
                this.locating();
                this.initAutoDropDown();//初始化自动搜索组件
            });
    }

    //初始化自动搜索组件
    initAutoDropDown(){
        let that = this;
        if(this.city ==""){
            this.city = "北京";
        }
        this.localSearch = new BMap.LocalSearch(this.city,
            {"onSearchComplete":function(result){
                if(result.tr != undefined){
                    that.creatPanel(result.tr);
                }
            }
        });
    }

    //拼装搜索结果下拉框
    creatPanel(data:any){
        let str:any = "";
        let ln = 0;
        if(data.length > 0 && data.length <5){
            ln = data.length;
        }else if(data.length > 5){
            ln = 5;
        }
        for(let i=0;i < ln;i++){
            if(data[i].title != undefined
                && data[i].point != undefined){
                let title = data[i].title;
                let point = data[i].point;
                str = str + "<li id='"+point.lng+","+point.lat+"' (click)='locationPoint(e)'>"+title+"</li>";
            }
        }

        let cont = document.getElementById("searchResultPanel");
        if(cont != null){
            cont.innerHTML = "";
            cont.innerHTML +=str;
        }
        cont.style.display = "block";
    }

    //下拉框点击事件
    locationPoint(){
        let addrStr = event.srcElement.innerHTML;
        let pointStr = event.srcElement.id;
        if(pointStr.length >1){
            //获取经纬度
            let lng = parseFloat(pointStr.split(",")[0]);
            let lat = parseFloat(pointStr.split(",")[1]);
            let point = new BMap.Point(lng,lat);
            //隐藏下拉框
            document.getElementById("searchResultPanel").style.display = "none";
            this.searchKey = addrStr;
            this.searchCache = addrStr;
            this.setPlace(point);//将地图移动到目标位置
        }
    }

    //获取点对象，将地图移至目标位置
    setPlace(point:any){
        this.map.clearOverlays();    //清除地图上所有覆盖物
        let signIcon = new BMap.Icon("./assets/imgs/sign1.png", new BMap.Size(20, 20));
        this.centerPoint = point.lng +","+ point.lat;
        this.map.centerAndZoom(point, 18);
        this.map.addOverlay(new BMap.Marker(point,{icon: signIcon}));    //添加标注
    }

    //值改变时搜索地址
    searchAddr(){
        // 当缓存地址不为空且输入地址不跟缓存一致的情况下，才进行查询，
        // 否则判定为当前是点击搜索结果造成的值改变
        if(this.searchCache == "" || this.searchKey != this.searchCache){
            if(this.searchKey.length >1){
                this.localSearch.search(this.searchKey);
            }
        }
    }

    //失去焦点时隐藏下拉框列表
    setShow(flag){
        if(flag == "true"){
            document.getElementById("searchResultPanel").style.display = "block";
        }else{
            setTimeout("document.getElementById(\"searchResultPanel\").style.display = \"none\"",100);
            // document.getElementById("searchResultPanel").style.display = "none";
        }
    }

    private locating(): void {
        // 百度地图API功能
        this.map = new BMap.Map("bmap");
        var point = new BMap.Point(116.331398,39.897445);
        this.map.centerAndZoom(point,18);
        var geolocation = new BMap.Geolocation();
        let that = this;

        let signIcon = new BMap.Icon("./assets/imgs/sign1.png", new BMap.Size(20, 20));

        this.map.addEventListener("movestart", function(){//添加地图拖动前事件
            that.map.clearOverlays();//清除覆盖物
            that.signFlag = true;
        });

        this.map.addEventListener("moveend", function(){//添加地图拖动后事件
            that.signFlag = false;
            let pt = that.map.getCenter();
            that.map.clearOverlays();
            var mk = new BMap.Marker(pt,{icon: signIcon});
            that.map.addOverlay(mk);

            let geoc = new BMap.Geocoder();
            geoc.getLocation(pt, function(rs){
                if(typeof(that.centerAddr)!= "undefined"){
                    that.centerPoint = rs.point.lng +","+rs.point.lat;
                    let addComp = rs.addressComponents;
                    //that.centerAddr = addComp.province + addComp.city + addComp.district + addComp.street + addComp.streetNumber;
                    if(that.centerAddrFlag){
                        that.centerAddr = rs.address;
                    }else{
                        that.centerAddr=that.address;
                        that.centerAddrFlag=true;
                    }
                    
                }
            });
        });

        this.map.addEventListener("zoomstart", function(){//添加地图缩放前事件
            that.map.clearOverlays();//清除覆盖物
            that.signFlag = true;
        });

        this.map.addEventListener("zoomend", function(){//添加地图缩放后事件
            that.signFlag = false;
            let pt = that.map.getCenter();
            that.map.clearOverlays();
            var mk = new BMap.Marker(pt,{icon: signIcon});
            that.map.addOverlay(mk);

            let geoc = new BMap.Geocoder();
            geoc.getLocation(pt, function(rs){
                if(typeof(that.centerAddr)!= "undefined"){
                    that.centerPoint = rs.point.lng +","+rs.point.lat;
                    let addComp = rs.addressComponents;
                    //that.centerAddr = addComp.province + addComp.city + addComp.district + addComp.street + addComp.streetNumber;
                    that.centerAddr = rs.address;
                   
                }
            });
        });
        geolocation.getCurrentPosition(function(r){
            if(this.getStatus() == "0"){
                var mk = new BMap.Marker(r.point,{icon: signIcon});
                that.map.addOverlay(mk);
                that.map.panTo(r.point);
                //map.addControl(new BMap.MapTypeControl({ mapTypes: ['BMAP_NORMAL_MAP'] }));
                //that.showAlert('您的位置：'+r.point.lng+','+r.point.lat);
            }
            else {
                //that.showAlert('failed'+this.getStatus());
            }
        },{enableHighAccuracy: true})
    }

    addStartIcon(point) {//添加起始位置图标
        var myIcon = new BMap.Icon("http://lbsyun.baidu.com/jsdemo/img/fox.gif", new BMap.Size(100, 100));
        var marker2 = new BMap.Marker(point, {icon: myIcon});  // 创建标注
        this.map.addOverlay(marker2);              // 将标注添加到地图中
    }
   
    delAddrCont(){
        let that=this;
        let alert = this.alertCtrl.create({
            title: '确定删除吗?',
            buttons: [
              {
                text: '取消',
                role: 'cancel',
                handler: data => {
                  console.log('Cancel clicked');
                }
              },
              {
                text: '确定',
                handler: data => {
                    that.myService.delAddrCont({id:that.id}).subscribe(data=>{
                        if(data){
                            that.showAlert("操作成功");
                            that.viewCtrl.dismiss({result:"1"});
                        }else{
                            that.showAlert("操作失败");
                            that.viewCtrl.dismiss({result:"0"});
                        }
                    });
                }
              }
            ]
          });
          alert.present();
    }
    newAddrCont(){
        
        this.myService.newAddrCont({id:this.id,contact:this.contact,address:this.centerAddr+this.cardAddr,telephone:this.telephone,point:this.centerPoint}).subscribe(data=>{
            if(data){
                this.showAlert("操作成功");
                this.viewCtrl.dismiss({result:"1"});
            }else{
                this.showAlert("操作失败");
                this.viewCtrl.dismiss({result:"0"});
            }
        });
    }
    showAlert(str:string) {
        let alert = this.alertCtrl.create({
            title: '',
            subTitle: str
        });
        alert.present();
    }
    checkcontact(){
       this.contacts.pickContact().then(cont=>{
        this.contact=(cont.name.familyName?cont.name.familyName:"")+(cont.name.givenName?cont.name.givenName:"");
            this.telephone=cont.phoneNumbers[0].value;
       }).catch(err=>{
           this.showAlert("获取联系人失败");
       });
    }
    getBack(){
        this.viewCtrl.dismiss();
    }
    getCardAddr(e){
        this.cardAddr=e.target.value;
    }
}



