import { Component } from '@angular/core';
import { NavController } from 'ionic-angular';
import  {ChargeService} from "../../providers/charge.service";
import {ChargeNextPage} from "./chargenext/chargenext";


@Component({
  selector: 'page-charge',
  templateUrl: 'charge.html'
})
export class ChargePage {
  RMB:number=0;
  PERCENT:any='0%';
  LEVEL_NAME:string;
  LEVEL_RIGHTS:string;
  USER_LEV:string;
  constructor(public navCtrl: NavController,private  chargeService:ChargeService) {


  }
    ionViewDidEnter() {
        this.chargeService.getChargeInfo().subscribe(data => {
            this.RMB=data[0].RMB==undefined?0:data[0].RMB;
            this.LEVEL_NAME=data[0].LEVEL_NAME==undefined?"":data[0].LEVEL_NAME;
            this.LEVEL_RIGHTS=data[0].LEVEL_RIGHTS==undefined?"":data[0].LEVEL_RIGHTS;
            this.USER_LEV=data[0].USER_LEV==undefined?"01":data[0].USER_LEV;
            let LEVEL_RMB=data[0].LEVEL_RMB==undefined?0:data[0].LEVEL_RMB;
            let LEVEL_RMB_END=data[0].LEVEL_RMB_END==undefined?0:data[0].LEVEL_RMB_END;

            this.PERCENT=(parseInt(this.USER_LEV.substr(1,1))-1)*33+(this.RMB-LEVEL_RMB)/(LEVEL_RMB_END-LEVEL_RMB)*33+'%';

        });    }
    chargeNext(){
        this.navCtrl.push(ChargeNextPage);
    }
}



