import { NavController } from "ionic-angular";

// 通用工具类
export class CommonUtil {
    constructor(){}
    /**
     * 获取 url 中的参数(获取微信code)
     * @returns {Object}
     */
    static getParams(url?: string): any {
        url = url || location.href;
        url += ' ';

        let params = {};
        let keys = url.match(/\w+(?=[=])/g);
        let values = url.match(/\w+(?=[&\s#])/g);

        if (keys) {
            for (let i = 0; i < keys.length; i++) {
                params[keys[i]] = values[i];
            }
        }

        console.log('获取url参数为：');
        console.log(params);
        return params;
    }

    /**
     * 判断是否有token
     * @param {string} name 对象名称
     * @returns {any}
     */
    static isTokenExist(): boolean {
        
        try {
            let tokenId = this.getStoredItem('tokenId').tokenId;
            return null != tokenId;
        } catch (error) {
            return false;
        }
    }

    /**
     * 从 localStorage 中获取对象
     * @param {string} name 对象名称
     * @returns {any}
     */
    static getStoredItem(name: string): any {
        return JSON.parse(localStorage.getItem(name));
    }

    /**
     * 从 localStorage 中删除对象
     * @param {string} name 对象名称
     * @returns {any}
     */
    static removeStoredItem(name: string): any {
        localStorage.removeItem(name);
    }
    
    /**
     * 将对象存储在 localStorage 中
     * @param {string} name 存储名称
     * @param {Object} obj 对象
     */
    static setStoredItem(name: string, obj: object): void {
        localStorage.setItem(name, JSON.stringify(obj));
    }

    // 空回调
    static emptyCallback = () => {
    };


    static formatNumber(n) {
        n = n.toString();
        return n[1] ? n : '0' + n
    }
    static formatTime(date, addDayCount = 0) {
        date.setDate(date.getDate() + addDayCount);
        const year = date.getFullYear();
        const month = date.getMonth() + 1;
        const day = date.getDate();
        const hour = date.getHours();
        const minute = date.getMinutes();
        const second = date.getSeconds();

        return [year, month, day].map(CommonUtil.formatNumber).join('-') + ' ' + [hour, minute, second].map(CommonUtil.formatNumber).join(':')
    }

    /**
     * 正确地返回到相应页面
     * @param {NavController} navCtrl 页面的NavController
     * @param {string} page 回退页面名称
     */
    static popTo(navCtrl: NavController, page: string): void {
        const VIEW_STACK = navCtrl.getViews();
        console.log(VIEW_STACK);
        const BACK_VIEW_CTRL = VIEW_STACK.find(view => view.index == Number(page));

        if (BACK_VIEW_CTRL) {
            navCtrl.popTo(BACK_VIEW_CTRL);
        } else {
            throw new Error('该页面不存在！');
        }
    }

    static isPhone(phone): boolean {
        return phone && phone.toString().length == 11;
    }

    static min2ZhHourMin(diffs: number) {
        let times = Math.abs(diffs);
        let hours = Math.floor(times / 60);
        let mins = times - hours * 60;
        let timeStr = (hours ? (hours + "小时") : "") + ((mins || !hours) ? (mins + "分钟") : "");
        return diffs >= 0 ? timeStr : ("-" + timeStr);
    }

    static min2ZhHourMinUnit(diffs: number) {
        let times = Math.abs(diffs);
        let hours = Math.floor(times / 60);
        let mins = times - hours * 60;
        let timeStr = (hours == 0 && mins != 30) && (mins + "分钟")
            || (hours == 0 && mins == 30) && "半小时"
            || (hours == 1 && mins == 0) && "小时"
            || (hours > 0 && mins == 0) && (hours + "小时")
            || (hours + "小时" + mins + "分钟");
        return diffs >= 0 ? timeStr : ("-" + timeStr);
    }

    static time2DoubleDigit(obj){
        let str = obj.toString();
        return str.length <= 1 ? ("0" + str) : str;
    }

    static time2ZhDay(date: Date) {
        let days = CommonUtil.diff(date, new Date(), CommonUtil.DAYS);
        return days == 0 && "今天"
            || (days == 1 && "昨天")
            || (days == 2 && "前天")
            || days == -1 && "明天"
            || (days == -2 && "后天")
            || (this.time2DoubleDigit(date.getMonth() + 1) + "-" + this.time2DoubleDigit(date.getDate()));
    }

    static time2ZhMonth(date: Date) {
        let months = CommonUtil.diff(date, new Date(), CommonUtil.MONTHS);
        return months == 0 && "本月"
            || (date.getMonth() + 1) + "月"
    }

    static SECONDS: number = 1;
    static MINUTES: number = 2;
    static HOURS: number = 3;
    static DAYS: number = 4;
    static MONTHS: number = 5;
    static YEARS: number = 6;

    private static SECONDS_NUM: number = 1000;
    private static MINUTES_NUM: number = 60000;
    private static HOURS_NUM: number = 3600000;


    private static diffTime(d1: Date, d2: Date, amount: number): number{
        let diff = (d1.getTime() - d2.getTime()) / amount;
        return diff >=0 ? Math.floor(diff) : Math.ceil(diff);
    }

    static diffDay(d1: Date, d2: Date): number{
        return CommonUtil.diffMonth(d1, d2) * 30 + d1.getDate() - d2.getDate();
    }

    static diffMonth(d1: Date, d2: Date): number{
        return CommonUtil.diffYear(d1, d2) * 12 + d1.getMonth() - d2.getMonth();
    }

    static diffYear(d1: Date, d2: Date): number{
        return d1.getFullYear() - d2.getFullYear();
    }

    static diff(d1: Date, d2: Date, filed: number){
        let amount: number;
        if(CommonUtil.SECONDS == filed){
            amount = CommonUtil.SECONDS_NUM;
        }else if(CommonUtil.MINUTES == filed){
            amount = CommonUtil.MINUTES_NUM;
        }else if(CommonUtil.HOURS == filed){
            amount = CommonUtil.HOURS_NUM;
        }else{
            amount = -1;
        }

        if(amount > 0){
            return CommonUtil.diffTime(d1, d2, amount);
        }else if(CommonUtil.DAYS == filed){
            return CommonUtil.diffDay(d1, d2);
        }else if(CommonUtil.MONTHS == filed){
            return CommonUtil.diffMonth(d1, d2);
        }
    }

}


