import { getAddrPage } from './../task/map/get_addr';
import { ComponentsUtil } from './../../util/components.util';
import { MyService } from './../../providers/my.service';
import { Component } from '@angular/core';
import { NavController, ViewController, ModalController } from 'ionic-angular';

@Component({
  selector: 'page-modal',
  templateUrl: 'modal.html'
})
export class ModalPage {
  addrContList:any;
  constructor(public navCtrl: NavController,private myservice:MyService,public viewCtrl: ViewController,public modalController:ModalController,public componentsUtil:ComponentsUtil) {
    this.getAddrContList();
  }
  getAddrContList(){
    this.myservice.getAddrCont().subscribe(data =>{
        this.addrContList=data;
    });
  }
  dismiss(i:number) {
    let data = this.addrContList[i];
    this.viewCtrl.dismiss(data);
  }
  newAddrCont(i:number){
    let modal=this.modalController.create(getAddrPage,this.addrContList[i]);
    modal.onDidDismiss(result=>{
        this.getAddrContList()
    });
    modal.present();
  }
  getBack(){
    this.viewCtrl.dismiss();
  }
  
  
 
  
}



