import {Injectable} from "@angular/core";
import {Observable} from "rxjs/Observable";
import {HttpWrapper} from "../util/http-wrapper.util";
import {CommonUtil} from "../util/model/common.util";


@Injectable()
export class UserService {

    session: string;
    private loginFlag: boolean = false;
    private user: {};

    constructor(private http: HttpWrapper) {
    }

    public loginUesr(user) {
        this.setUser(user);
        this.setLogin(true);
    }

    public logout() {
        this.setUser(null);
        this.setLogin(false);
    }

    public setLogin(flag: boolean) {
        this.loginFlag = flag;
    }
    public setUser(user){
        this.user = user;
    }

    reLogin() {
        // this.http.post("/login/reLoad").subscribe(data => {
        //     this.setLogin(true);
        //     this.user = data;
        // });
    }

    isLogin() {
        return this.loginFlag;
    }

    getUser() {
        return this.user;
    }

}



