import { Injectable } from "@angular/core";
import { Observable } from "rxjs/Observable";
import {HttpWrapper} from "../util/http-wrapper.util";


@Injectable()
export class ExchangeService {

    constructor(private http: HttpWrapper) {
    }

    /**
     * 通过服务端请求获得任务列表
     * @param fileId
     */
    getConfig(configKey:String): Observable<any> {
        const URL = '/exchange/getConfig';
        return this.http.post(URL, configKey,{showLoading: false, showError: true});
    }
   
    getCoinInfo(): Observable<any> {
        const URL = '/exchange/getCoinInfo';
        return this.http.post(URL, null,{showLoading: false, showError: true});
    }
   
    exchangeTelefare(obj:any): Observable<any> {
        const URL = '/exchange/exchangeTelefare';
        return this.http.post(URL, obj,{showLoading: false, showError: true});
    };
    exchangeProp(obj:any): Observable<any> {
        const URL = '/exchange/exchangeProp';
        return this.http.post(URL, obj,{showLoading: false, showError: true});
    };
    exchangeMoney(obj:any): Observable<any> {
        const URL = '/exchange/exchangeMoney';
        return this.http.post(URL, obj,{showLoading: false, showError: true});
    };
   
}



