import {Component, OnDestroy, OnInit} from '@angular/core';
import {NavController, NavParams, Platform} from 'ionic-angular';
import {HomeService} from "../../../providers/home.service";
import {Geolocation} from "@ionic-native/geolocation";
import "rxjs/add/operator/filter";
import "rxjs/add/operator/takeWhile";
import {ConfirmOrderPage} from "../confirm/confirm_order";
import {HomePage} from "../../home/home";

declare let BMap: any;
declare let baidumap_location;

@Component({
    selector: 'page-executing',
    templateUrl: 'executing.html'
})
export class ExecutingPage implements OnInit, OnDestroy {
    alive: boolean;
    stations: Array<any>;
    picId: string;
    // 地图开启标志
    mapOn = true;
    // 搜索关键字
    searchKey = '';
    // 地图对象
    map: any;
    // 当前位置对象
    locationMarker: any;
    // 当前位置
    private locationPoint: any;
    initLocate :any;
    private address:any;
    private task:any;
    private province:any;//当前省份
    private city:any;//当前城市

    constructor(public navCtrl: NavController,
                private param: NavParams,
                private geolocation: Geolocation,
                private platform: Platform,
                private homeService: HomeService) {
        this.task = param.get("task");
        // this.address = '郑州火车站';
    }

    ngOnInit(): void {
        console.log('1`23');
        this.homeService.loadScript()
            .subscribe(() => {
                // 定位
                this.locating();
            });
    }

    // 页面控件初始化完成，加载数据
    ionViewWillEnter(): void {
        this.alive = true;
    }

    ionViewDidLeave(): void {
        this.alive = false;
    }

    // 页面销毁
    ngOnDestroy(): void {
        this.alive = false;
    }


    private locating(): void {
        let that: any = this;
        console.log('调用百度地图定位插件定位...');
        baidumap_location.getCurrentPosition((result) => {
            that.city = result.city;//当前所在城市
            let pointStr = result.addr;//当前详细地址
            let point: any = new BMap.Point(result.longitude, result.latitude);//由经纬度获取一个点
            that.map.centerAndZoom(point, 11);
            let walking = new BMap.WalkingRoute(that.map, {renderOptions: {map: that.map, autoViewport: true}});
            let areaStr = that.task.ADDRESS.replace(/,/g, '');
            walking.search(pointStr , areaStr);//规划线路【起点，终点】
            let starIcon = new BMap.Icon('./assets/imgs/dh01.png', new BMap.Size(50, 50), {
                anchor: new BMap.Size(20, 35)
            });//创建图标
            walking.setMarkersSetCallback(function (res) {//添加图标
                res[0].marker.setIcon(starIcon);
            });
        });
    }

    addStartIcon(map,point) {//添加起始位置图标
        var myIcon = new BMap.Icon("http://lbsyun.baidu.com/jsdemo/img/fox.gif", new BMap.Size(100, 100));
        var marker2 = new BMap.Marker(point, {icon: myIcon});  // 创建标注
        map.addOverlay(marker2);              // 将标注添加到地图中
    }

    //接受任务者确定完成任务，添加照片页面
    toConfirmOrder(){
        this.navCtrl.push(ConfirmOrderPage,{task:this.task});
        this.alive = false;
    }

    toHome(){//跳转到主页
        this.navCtrl.push(HomePage);
    }


}



