import { ComponentsUtil } from './../../../util/components.util';
import { FriendService } from './../../../providers/friend.service';
import { Component } from '@angular/core';
import { IonicPage, NavController, NavParams, Icon } from 'ionic-angular';
import {UserService} from "../../../providers/user.service";
import {FriendInfoPage} from "../friendinfo/friendinfo";

@Component({
  selector: 'page-comments',
  templateUrl: 'comments.html',
})
export class CommentsPage {
     comments:any;
     friendId:any;
  constructor(public navCtrl: NavController,private userService: UserService,private friendService:FriendService, private param:NavParams,private componentsUtil: ComponentsUtil) {
    this.friendId=param.data.friendId;
    this.comments=param.data.comments;
}
    modComments(){
        this.friendService.modComments(this.friendId,this.comments).subscribe(data => {
            if(data){
                this.componentsUtil.showAlert("保存成功");
                this.navCtrl.pop();
            }
        });
    }



}



