import {Injectable} from "@angular/core";
import {Observable} from "rxjs/Observable";
import {HttpWrapper} from "../util/http-wrapper.util";
import {LoginCoreModel} from "../models/LoginCoreModel";
import {CommonUtil} from "../util/model/common.util";
import {UserService} from "./user.service";


@Injectable()
export class LoginService {

    constructor(private http: HttpWrapper,
                private userService: UserService) {
    }

    newSession(): Observable<any> {
        const URL = '/session/new';
        return this.http.post(URL, null, {showError: false, withSession: "N"});
    }

    autoLogin(): Observable<any> {
        const URL = '/login/autoLogin';
        return this.http.post(URL, null, {showError: false}).map(val => {
            this.userService.loginUesr(val);
        });
    }

    reLogin(): Observable<any> {
        if(CommonUtil.getStoredItem("sessionId")){
            return this.autoLogin().catch(err => {
                if(err == "NOT_VALID_SESSION"){
                    CommonUtil.removeStoredItem("sessionId");
                    return this.newSession().map(val => {
                        // return Observable.throw("用户未登录");
                        CommonUtil.setStoredItem("sessionId", val);
                        throw("用户未登录");
                    });
                }else{
                    throw(err);
                }
            });
        }else{
            return this.newSession().map(val => {
                // return Observable.throw("用户未登录");
                CommonUtil.setStoredItem("sessionId", val);
                throw("用户未登录");
            });
        }
    }

    sendSms(loginCode: string): Observable<any> {
        const URL = '/login/sendSms';
        return this.http.post(URL, loginCode);
    }

    validSms(loginCode: LoginCoreModel): Observable<any> {
        const URL = '/login/validSms';
        return this.http.post(URL, loginCode);
    }

    doLogin(model: LoginCoreModel): Observable<any> {
        const URL = '/login/doLogin';
        return this.http.post(URL, model).map(val => {
            this.userService.loginUesr(val);
        });
    }

    sendRegisterSms(loginCode: string): Observable<any> {
        const URL = '/login/sendRegisterSms';
        return this.http.post(URL, loginCode);
    }

    doRegister(model: LoginCoreModel): Observable<any> {
        const URL = '/login/doRegister';
        return this.http.post(URL, model);
    }

    setPwd(model: any): Observable<any> {
        const URL = '/login/setPwd';
        return this.http.post(URL, model);
    }

    reSetPwd(model: any): Observable<any> {
        const URL = '/login/reSetPwd';
        return this.http.post(URL, model);
    }
    changePwd(model: any): Observable<any> {
        const URL = '/login/changePwd';
        return this.http.post(URL, model);
    }
    logout() {
        const URL = '/login/logout';
        return this.http.post(URL).map(val => {
            CommonUtil.removeStoredItem("sessionId");
            throw "success";
        }).catch(err => {
            if(err == "success"){
                return this.newSession().map(val => {
                    // return Observable.throw("用户未登录");
                    CommonUtil.setStoredItem("sessionId", val);
                });
            }
        });
    }
}



