import { FriendService } from './../../../providers/friend.service';
import { Component } from '@angular/core';
import { IonicPage, NavController, NavParams, Icon } from 'ionic-angular';
import {UserService} from "../../../providers/user.service";
import {CommentsPage} from "../comments/comments";
import {MessagePage} from "../message/message";


@Component({
  selector: 'page-friendinfo',
  templateUrl: 'friendinfo.html',
})
export class FriendInfoPage {
     friendInfo:any
  constructor(public navCtrl: NavController,private userService: UserService,private friendService:FriendService, private param:NavParams) {
    this.friendInfo=param.data.friendInfo;
    this.friendInfo.PIC_ID?this.friendInfo.PIC_ID:"";
    this.friendInfo.COMMENTS?this.friendInfo.COMMENTS:"";

  }
    goComments(){
        this.navCtrl.push(CommentsPage,{friendId:this.friendInfo.USER_ID,comments:this.friendInfo.COMMENTS});
    }
    goMessage(){
        this.navCtrl.push(MessagePage,{friendId:this.friendInfo.USER_ID});
    }




}



