import { Injectable } from "@angular/core";
import { Observable } from "rxjs/Observable";
import {HttpWrapper} from "../util/http-wrapper.util";


@Injectable()
export class ChargeService {

    constructor(private http: HttpWrapper) {
    }

    /**
     * 通过服务端请求获得任务列表
     * @param fileId
     */
    getChargeInfo(): Observable<any> {
        const URL = '/charge/getChargeInfo';
        return this.http.post(URL, null,{showLoading: false, showError: true});
    }
    newOrder(orderInfo): Observable<any> {
        const URL = '/wallet/rech';
        return Observable.create(subscribe => {
            this.http.post(URL, orderInfo)
                .subscribe(value => {
                    subscribe.next(value);
                })
        })
    }
}



