import { FriendService } from './../../providers/friend.service';

import {Component} from '@angular/core';
import {NavController, Platform} from 'ionic-angular';





@Component({
    selector: 'page-friendrank',
    templateUrl: 'friendrank.html'
})
export class FriendRankPage {

    tabs: string = "day";
    friends:any;
    constructor(public navCtrl: NavController,public friendService:FriendService) {


    }
    ionViewDidEnter() {
        this.getFriendList();
      }
    getFriendList(){
        this.friendService.friendRank(this.tabs).subscribe(data => {
            this.friends=data;
        });
    }
    segmentChanged(e){
        this.getFriendList();
    }

}



