export class TaskAddModel {

    classify: string; //任务分类，10：悬赏，11：挑战
    type: string; // 任务类型，01：跑腿，02：日常，03：问答
    bigType: string; // 任务大类，11镖局、12代购；21一起玩、22一起吃、23一起旅游、24江湖救急；31生活常识、32医学、33科学、34汽车
    smallType: string; // 任务小类,1101钥匙、1102鲜花、1103蛋糕、1104文件、1105食品饮料、1106水果生鲜、1107证照证件、1199其他；2201全部、2202甜品、2203清单、2204素食、2205麻辣、2206养生、2207火锅、2208视觉、2299其他
    description: string; // 任务描述
    area: string; // 任务区域，包括省、市、县区
    fromAddr: string; // 任务详细地点
    toAddr: string; // 任务目的地
    fromMan: string; // 开始联系人
    toMan: string; // 接收联系人
    fromTelephone: string; // 开始联系人电话
    toTelephone: string; // 接收联系人电话
    coin: number; // 奖励金额
    level: number; // 任务等级
    subscribeTime: Date; // 预约时间
    partnerGender: string; //同伴性别：男01，女02，保密03
    feeType: string; //费用结算：01我请客，02你请客，03AA
    expireTime: Date; //任务到期时间
}



