import { Component } from '@angular/core';
import { NavController } from 'ionic-angular';
import { TaskService} from "../../../providers/task.service";
import {CommonUtil} from "../../../util/model/common.util";
import {DetailPage} from "../detail/detail";
import {AlreadyDetailPage} from "../detail/already_detail";
import {OrderMapPage} from "../map/order_map";
import {DbDict} from "../../../util/DbDict";

@Component({
    selector: 'page-al-accept',
    templateUrl: 'already_accept.html'
})
export class AlreadyAcceptPage {
    taskList: Array<any>;
    picId: string;

    constructor(public navCtrl: NavController,
                private taskService: TaskService) {
    }

    ionViewWillEnter() {
        this.getAlreadyAccept();
    }

    getAlreadyAccept() {

        //查询已接取的任务
        let param :any = {};
        param.state = '02';//状态是已接取
        this.taskService.getAlreadyAccept(param).subscribe(data => {
            this.taskList = data;
            for(let i=0;i < data.length;i++){

                //任务分类大类型标准代码转中文描述
                this.taskList[i].BIG_TYPE_STR = DbDict.code2type(this.taskList[i].BIG_TYPE, "TASK_BIG_TYPE");
            }
        }, CommonUtil.emptyCallback);
    }

    public showDetail(task){//展示任务明细
        this.navCtrl.push(AlreadyDetailPage,{task: task});
    }

    //展示地图
    showMap(task){
        this.navCtrl.push(OrderMapPage,{task: task});
    }

}



