import { ExchangePropPage } from './exchangeprop/exchangeprop';
import { ExchangeMoneyPage } from './exchangemoney/exchangemoney';
import { ExchangeTelefarePage } from './exchangetelefare/exchangetelefare';
import { Component } from '@angular/core';
import { NavController } from 'ionic-angular';
@Component({
  selector: 'page-exchange',
  templateUrl: 'exchange.html'
})
export class ExchangePage {

  constructor(public navCtrl: NavController) {

  }
  exchange(type:string){
    if(type=='1'){
      this.navCtrl.push(ExchangeTelefarePage);
    }else if(type=='2'){
      this.navCtrl.push(ExchangePropPage);
    }else if(type=='3'){
      this.navCtrl.push(ExchangeMoneyPage);
    }
  }
}



