import { ComponentsUtil } from './../../../util/components.util';
import { FriendService } from './../../../providers/friend.service';
import { Component } from '@angular/core';
import { IonicPage, NavController, NavParams, Icon } from 'ionic-angular';
import {UserService} from "../../../providers/user.service";
import {CommentsPage} from "../comments/comments";
import {MessagePage} from "../message/message";


@Component({
  selector: 'page-unfriendinfo',
  templateUrl: 'unfriendinfo.html',
})
export class UnFriendInfoPage {
     friendInfo:any
  constructor(public navCtrl: NavController,private userService: UserService,private friendService:FriendService, private param:NavParams,private componentsUtil:ComponentsUtil) {
       this.friendInfo=param.data.friendInfo;
       this.friendInfo.PIC_ID?this.friendInfo.PIC_ID:"";

  }
    addFriend(){
        this.friendService.addFriend(this.friendInfo.USER_ID).subscribe(data => {
            if(data){
                this.componentsUtil.showAlert("添加成功");
            }else{
                this.componentsUtil.showAlert("添加失败");
            }
        });
    }





}



