import {Component, Injector} from '@angular/core';
import {App, NavController, NavParams} from 'ionic-angular';
import {LoginService} from "../../../providers/login.service";
import {APP_CONFIG, AppConfig} from "../../../models/app-config";
import {AppCfgProperties} from "../../../providers/appConfig.properties";
import {CommonUtil} from "../../../util/model/common.util";
import {HomePage} from "../../home/home";
import {UserService} from "../../../providers/user.service";

/**
 * Generated class for the ChangePwdPage page.
 *
 * See https://ionicframework.com/docs/components/#navigation for more info on
 * Ionic pages and navigation.
 */

@Component({
    selector: 'paget-register',
    templateUrl: 'register.html',
})
export class RegisterPage {
    countdownNum: number;
    userName: string;
    smsCode: string;

    // 以下注册成功之后才用到的属性
    registerSuccess: boolean = false;
    shortName: string;
    password: string;

    constructor(public navCtrl: NavController,
                private param: NavParams,
                private appConfig: AppCfgProperties,
                private loginService: LoginService,
                private userService: UserService) {
        this.userName = this.param.get("userName");
    }

    userNameValid(): boolean{
        return this.userName && (this.userName.length == 11 || this.userName.length == 18);
    }

    sendRegisterSms() {
        this.loginService.sendRegisterSms(this.userName).subscribe(data => {
            this.smsCountDown();
        }, CommonUtil.emptyCallback);
    }

    doRegister() {
        const DATA = {
            userName: this.userName + "",
            smsCode: this.smsCode + ""
        };
        this.loginService.doRegister(DATA).subscribe(data => {
            this.registerSuccess = true;
        }, CommonUtil.emptyCallback);
    }

    //获取验证码
    smsCountDown() {
        this.countdownNum = this.appConfig.getInstance().smsInterval;
        let inter = setInterval(() => {
            if (this.countdownNum) {
                --this.countdownNum;
            }else{
                clearInterval(inter);
            }
        }, 1000)
    }

    setPwd() {
        const DATA = {
            password: this.password,
            shortName: this.shortName
        }
        this.loginService.setPwd(DATA).subscribe(val => {
            this.userService.loginUesr(val);
            this.navCtrl.setRoot(HomePage);
        }, CommonUtil.emptyCallback);

    }

    goBack() {
        this.navCtrl.pop();
    }
}



