import { UserService } from './../../providers/user.service';
import { HomePage } from './../home/home';
import { LoginService } from './../../providers/login.service';
import { ComponentsUtil } from './../../util/components.util';
import { Component } from '@angular/core';
import { NavController } from 'ionic-angular';
import {AcceptPage} from "../task/accept/accept";

@Component({
  selector: 'page-password',
  templateUrl: 'password.html'
})
export class PassWordPage {
  passwordOld:string;
  passwordNew:string;
  constructor(public navCtrl: NavController,public comutil:ComponentsUtil,private loginService:LoginService, private userService:UserService) {

  }

    loadData() {
        this.navCtrl.push(AcceptPage);
    }
    changePwd(){
      if(this.passwordOld==""||this.passwordOld==null){
        this.comutil.showAlert("请输入原密码");
        return;
      }
      if(this.passwordNew==""||this.passwordNew==null){
        this.comutil.showAlert("请输入修改的密码");
        return;
      }
      this.loginService.changePwd({passwordOld:this.passwordOld,passwordNew:this.passwordNew}).catch(err => {
        
        return err;
     }).subscribe(val => {
      if(typeof(val)!="string"){
        this.userService.loginUesr(val);
        this.navCtrl.setRoot(HomePage);
      }
        
    });
    }
}



