import { Component } from '@angular/core';
import { NavController } from 'ionic-angular';
import { TaskService} from "../../../providers/task.service";
import {CommonUtil} from "../../../util/model/common.util";
import {TaskListPage} from "../taskList/taskList";

@Component({
    selector: 'page-accept',
    templateUrl: 'accept.html'
})
export class AcceptPage {
    stations: Array<any>;
    picId: string;

    constructor(public navCtrl: NavController,
                private taskService: TaskService) {
    }

    //查询可接任务，状态 【STATE = '01'】
    getTaskList(type:string) {
        this.navCtrl.push(TaskListPage,{type:type});
    }

}



