import { Component } from '@angular/core';
import { NavController, ViewController, ModalController, NavParams, AlertController } from 'ionic-angular';

@Component({
  selector: 'page-pictures',
  templateUrl: 'pictures.html'
})
export class PicturesPage {
  pictures:string;
  //"4c053c82-ed1e-4ae3-a3de-1c9a92fc5da2,4c053c82-ed1e-4ae3-a3de-1c9a92fc5da2,486059ae-8da6-4936-9247-f482049906e9";
  picturesList:any;
  //=["4c053c82-ed1e-4ae3-a3de-1c9a92fc5da2,4c053c82-ed1e-4ae3-a3de-1c9a92fc5da2","486059ae-8da6-4936-9247-f482049906e9"];
  constructor(public navparams:NavParams,public navCtrl: NavController,public viewCtrl: ViewController,public modalController:ModalController,public alertCtrl :AlertController) {
    //this.pictures=this.navparams.get("imgIdsStr");
    if(this.pictures.indexOf&&this.pictures.indexOf(",")>0){
      this.picturesList=this.pictures.split(",");
    }else{
      this.picturesList.push(this.pictures);
    }
  }
  
  getBack(){
    for(let i=0;i<this.picturesList.length;i++){
      if(i==0){
        this.pictures=this.picturesList[i];
      }else{
        this.pictures+=","+this.picturesList[i];
      }
    }
    this.viewCtrl.dismiss({imgIdsStr:this.pictures});
  }
  delImg(flag:number){
    let alert = this.alertCtrl.create({
      title: '确定删除吗?',
      buttons: [
        {
          text: '取消',
          role: 'cancel',
          handler: data => {
            console.log('Cancel clicked');
          }
        },
        {
          text: '确定',
          handler: data => {
            this.picturesList.splice(flag,1);
            if(this.picturesList.length==0){
              this.viewCtrl.dismiss({imgIdsStr:""});
            }
          }
        }
      ]
    });
    alert.present();



    
  }
  
  
 
  
}



