import { Injectable } from "@angular/core";
import { Observable } from "rxjs/Observable";
import {HttpWrapper} from "../util/http-wrapper.util";


@Injectable()
export class MyService {

    constructor(private http: HttpWrapper) {
    }
    //获取当前人的常用地址及联系人
    getAddrCont(): Observable<any> {
        const URL = '/my/getAddrCont';
        return this.http.post(URL, null,{showLoading: false, showError: true});
    }
    /**
     * 通过服务端请求获得当前登陆人信息
     * @param fileId
     */
    getMyInfo(): Observable<any> {
        const URL = '/my/getMyInfo';
        return this.http.post(URL, null,{showLoading: false, showError: true});
    }
    getAreaList(): Observable<any> {
        const URL = '/my/getAreaList';
        return this.http.post(URL, null,{showLoading: false, showError: true});
    }
    /**
     * 修改个人信息
     * @param fileId
     */
    updateMyInfo(param: any): Observable<any> {
        const URL = '/my/updateMyInfo';
        return this.http.post(URL, param, {showLoading: false, showError: true});
    };
    updateTaskInfo(param: any): Observable<any> {
        const URL = '/my/updateTaskInfo';
        return this.http.post(URL, param, {showLoading: false, showError: true});
    };
    uploadFile(data: any): Observable<any> {
        const URL = '/file/upload';
        console.log(data);
        return this.http.post(URL, data, {showLoading: false, showError: true});
    };
    updateMyPic(param: any): Observable<any> {
        const URL = '/my/updateMyPic';
        return this.http.post(URL, {picId:param}, {showLoading: false, showError: true});
    };
    newAddrCont(param: any): Observable<any> {
        const URL = '/my/newAddrCont';
        return this.http.post(URL, param, {showLoading: false, showError: false});
    };
    delAddrCont(param: any): Observable<any> {
        const URL = '/my/delAddrCont';
        return this.http.post(URL, param, {showLoading: false, showError: false});
    };
}



