import {ReleaseHistoryPage} from './../taskList/release_history';
import {ModalPage} from './../../modal/modal';
import {Component} from '@angular/core';
import {AlertController, ModalController, NavController, NavParams} from 'ionic-angular';
import {TaskService} from "../../../providers/task.service";
import {CommonUtil} from "../../../util/model/common.util";
import {ChargePage} from "../../charge/charge";
import {ReleasePage_4} from "./release_4";
import {TaskAddModel} from "../../../models/TaskAddModel";
import {ComponentsUtil} from "../../../util/components.util";
import {ResponseData} from "../../../models/response-data";
import {DbDict} from "../../../util/DbDict";

@Component({
    selector: 'page-release-3-daily',
    templateUrl: 'release-3-daily.html'
})
export class Release3DailyPage {

    // 难度等级是否可以重置
    canResetLevel: boolean = true;
    // 最低金币
    minCoin: number = 1;
    areasColumns = DbDict.getAreas();

    // 金币等级关系
    starsCoin = [{
        minCoin: 1,
        coin: 2000
    }, {
        minCoin: 2001,
        coin: 4999
    }, {
        minCoin: 5000,
        coin: 9999
    }, {
        minCoin: 10000,
        coin: 49999
    }, {
        minCoin: 50000,
        coin: 99999
    }];

    // 一起吃类型选择
    eatTypes;
    partnerGenderTypes;
    feeTypes;

    starLevel: number = 0; //难度等级，<=此下标的星星都是活跃状态

    task: TaskAddModel = new TaskAddModel();
    taskDescript: any = {};
    personalCoin: number;

    constructor(public navCtrl: NavController,
                private param: NavParams,
                public alertCtrl: AlertController,
                private taskService: TaskService,
                private componentUtil: ComponentsUtil,
                public modalController: ModalController) {
        this.initTypes();
        let taskInfo = param.get("taskInfo");
        console.log(taskInfo);
        this.personalCoin = parseInt(taskInfo.coin_personal);
        this.task.type = taskInfo.typeCode;
        this.task.bigType = taskInfo.type_2_code;
        this.initTask();
    }

    // 页面控件初始化完成，加载数据
    ionViewDidEnter(): void {
        console.log('页面加载完毕');
    }

    releaseTask() {//发布任务信息完整性校验

        let msg = "", request = this.task;

        request.level = this.starLevel;
        request.classify = '10'; //悬赏任务

        // 21一起玩、22一起吃、23一起旅游、24江湖救急
        if(this.task.bigType == '21'){
            msg = this.playTogetherValidate(request);
        }else if(this.task.bigType == '22'){
            msg = this.eatTogetherValidate(request);
        }else if(this.task.bigType == '23'){
            msg = this.travelTogetherValidate(request);
        }else if(this.task.bigType == '24'){
            msg = this.jhjjValidate(request);
        }else if(this.task.bigType == '25'){
            msg = this.otherValidate(request);
        }else if(this.task.type == '03'){
            msg = this.questionValidate(request);
        }

        if(msg){
            this.componentUtil.showAlert(msg);
            return false;
        }

        if(this.personalCoin < request.coin){
            this.showCoinUnEnoughAlert();
            return false;
        }

        this.componentUtil.showLoading("发布中...");
        this.taskService.addTask(request).subscribe(data => {
            //发布完成任务自动跳转到历史发布界面
            this.componentUtil.hideLoading();
            this.navCtrl.push(ReleaseHistoryPage);
        },(error: ResponseData) => {
            this.componentUtil.hideLoading();
            if (error.message == "COIN_UN_ENOUGH") {
                this.showCoinUnEnoughAlert();
            }else {
                this.componentUtil.showAlert(error.message);
            }
        });
    }

    //跳转获取联系人及地址模板
    checkFromContacts() {
        let modal = this.modalController.create(ModalPage);
        modal.onDidDismiss(data => {
            this.task.fromMan = data.CONTACT;
            this.task.fromAddr = data.ADDRESS;
            this.task.fromTelephone = data.TELEPHONE;
        });
        modal.present();
    };

    checkToContacts() {
        let modal = this.modalController.create(ModalPage);
        modal.onDidDismiss(data => {
            this.task.toMan = data.CONTACT;
            this.task.toAddr = data.ADDRESS;
            this.task.toTelephone = data.TELEPHONE;
        });
        modal.present();
    };

    setLevel(star, num) {
        if(!this.canResetLevel){
            return false;
        }
        this.setStarActiveUntil(num);
        this.task.coin = star.coin;
    }

    setStarActiveUntil(num) {
        this.starLevel = num;
    }

    onCoinChange(){
        let coin = !this.task.coin.toString() ? 0 : parseInt(this.task.coin.toString());
        let arr = this.starsCoin;

        let length = arr.length;
        let min = arr[0].minCoin,
            max = arr[length - 1].coin;


        for(let i = 0; i < arr.length; i++){
            if(coin < min){
                this.setStarActiveUntil(0);
                break;
            }else if(arr[i].coin >= coin && arr[i].minCoin <= coin){
                this.setStarActiveUntil(++i);
                break;
            }else if(coin > max){
                this.setStarActiveUntil(length);
                break;
            }
        }
    }

    requestBasicValidate(request): string {
        return !request.description && "请输入任务描述"
            || !request.subscribeTime && "请输入任务开始的具体时间"
            || !request.toMan && "请输入目的地联系人姓名"
            || !request.toTelephone && "请输入目的地联系电话"
            || !CommonUtil.isPhone(request.toTelephone) && "目的地联系电话格式不正确"
            || !request.toAddr && "请输入目的地地址"
            || request.coin < this.minCoin && `金币最少不能低于${this.minCoin}`
            || !request.level && "请选择任务难度"
            || "";
    }

    playTogetherValidate(request): string{
        var msg = this.requestBasicValidate(request);
        return msg && msg
            || !request.partnerGender && "请选择同伴性别"
            || !request.feeType && "请选择费用结算类型";
    }
    eatTogetherValidate(request): string{
        var msg = this.requestBasicValidate(request);
        return msg && msg
            || !request.partnerGender && "请选择同伴性别"
            || !request.feeType && "请选择费用结算类型"
            || !request.smallType && "请选择伙食类型"
            || "";
    }
    travelTogetherValidate(request): string{
        var msg = this.requestBasicValidate(request);
        return msg && msg
            || (!request.area || request.area == "添加标签") && "请添加标签"
            || !request.fromMan && "请填写集合人姓名"
            || !request.fromAddr && "请填写集合地址"
            || !request.fromTelephone && "请填写集合人电话"
            || !request.partnerGender && "请选择同伴性别"
            || !request.feeType && "请选择费用结算类型"
            || !CommonUtil.isPhone(request.fromTelephone) && "集合人联系方式格式不正确"
            || !(request.area.split("-")[1].substring(0, 2) == request.toAddr.substring(0, 2)) && "标签与目的地不符"
            || "";
    }
    jhjjValidate(request): string{
        var msg = this.requestBasicValidate(request);
        return msg && msg
            || !request.fromAddr && "请填写坐标地址"
            || "";
    }
    otherValidate(request): string{
        var msg = this.requestBasicValidate(request);
        return msg && msg
            || !request.fromMan && "请填写集合人姓名"
            || !request.fromAddr && "请填写集合地址"
            || !request.fromTelephone && "请填写集合人电话"
            || !request.partnerGender && "请选择同伴性别"
            || !request.feeType && "请选择费用结算类型"
            || !CommonUtil.isPhone(request.fromTelephone) && "集合人联系方式格式不正确"
            || "";
    }
    questionValidate(request): string{
        return !request.description && "请输入问题描述"
            || !request.expireTime && "请输入任务截止时间"
            || request.coin < this.minCoin && `金币最少不能低于${this.minCoin}`
            || !request.level && "请选择任务难度"
            || "";
    }

    showCoinUnEnoughAlert() {
        let that = this;
        this.alertCtrl.create({
            title: "提示",
            message: "任务扣除后不足500金币,是否到充值页面?",
            buttons: [
                {
                    text: '取消',
                    role: 'cancel',
                    handler: function () {
                        console.log('选择取消按钮');
                    }
                }, {
                    text:  '确认',
                    handler:  function () {
                        that.navCtrl.push(ChargePage);
                    }
                }
            ]
        }).present();
    }

    initTypes() {
        let types = [];
        "2202甜品 2203清单 2204素食 2205麻辣 2206养生 2207火锅 2208视觉 2299其他".split(" ").forEach(val => {
            types.push({
                codeType: val.substring(0, 4) ,
                typeName: val.substring(4)
            })
        });
        this.eatTypes = types;
        this.partnerGenderTypes = DbDict.getTypes('TASK_PARTNER_GENDER');
        this.feeTypes = DbDict.getTypes('TASK_FEE_TYPE');
    }

    getTypeName(code, type) {
        return DbDict.code2type(code, type);
    }

    initTask() {
        // 21-一起玩 22-一起吃 23-一起旅游 24-江湖救急
        if(this.task.bigType == '21'){
            this.taskDescript.placeholder = "请输入任务描述";
            this.taskDescript.toAddrDescript = "目的地信息与联系人方式";
        }else if(this.task.bigType == '22'){
            this.taskDescript.placeholder = "请输入任务描述，如：一起吃海底捞火锅";
            this.taskDescript.toAddrDescript = "目的地信息与联系人方式";
        }else if(this.task.bigType == '23'){
            this.taskDescript.placeholder = "请输入任务描述，如：“上海自驾游1日”";
            this.taskDescript.toAddrDescript = "目的地信息与联系人方式";
        }else if(this.task.bigType == '24'){
            this.canResetLevel = false;
            this.minCoin = 100000;
            this.starLevel = 5;
            this.taskDescript.placeholder = "请输入您的需求，如：出了车祸，人走不开，狗狗急需送医救治。我在坐标1，需送往坐标2。";
            this.taskDescript.toAddrDescript = "坐标2与联系人方式";
        }else{
            this.taskDescript.placeholder = "请输入任务描述";
            this.taskDescript.toAddrDescript = "目的地信息与联系人方式";
        }
    }

}



