import { Injectable } from "@angular/core";
import { Observable } from "rxjs/Observable";
import {HttpWrapper} from "../util/http-wrapper.util";


@Injectable()
export class FriendService {

    constructor(private http: HttpWrapper) {
    }

    /**
     * 通过服务端请求获得任务列表
     * @param fileId
     */
    getFriendList(type: string): Observable<any> {
        const URL = '/friend/getFriendList';
        return this.http.post(URL, type,{showLoading: false, showError: true});
    }
    getUnFriendList(type: string): Observable<any> {
        const URL = '/friend/getUnFriendList';
        return this.http.post(URL, type,{showLoading: false, showError: true});
    }
    friendRank(type:string):Observable<any>{
        const URL = '/friend/friendRank';
        return this.http.post(URL, type,{showLoading: false, showError: true});
    }
    removeFriend(type: string): Observable<any> {
        const URL = '/friend/removeFriend';
        return this.http.post(URL, type,{showLoading: false, showError: true});
    };
    addFriend(type: string): Observable<any> {
        const URL = '/friend/addFriend';
        return this.http.post(URL, type,{showLoading: false, showError: true});
    };
    getFriendInfo(type: string): Observable<any> {
        const URL = '/friend/getFriendInfo';
        return this.http.post(URL, type,{showLoading: false, showError: true});
    }
    modComments(FRIEND_ID:string,comments:string){
        const URL = '/friend/modComments';
        return this.http.post(URL, {friendId:FRIEND_ID,comments:comments},{showLoading: false, showError: true});
    }
}



