import {Injectable, Injector, NgModule} from "@angular/core";
import {ComponentsUtil} from "./components.util";
import {HttpClient, HttpErrorResponse, HttpHeaders} from "@angular/common/http";
import {Observable} from "rxjs/Observable";
import {isUndefined} from "util";
import {isPresent} from "ionic-angular/util/util";
import "rxjs/add/operator/timeoutWith";
import "rxjs/add/observable/throw";
import "rxjs/add/operator/map";
import "rxjs/add/operator/catch";
import {ResponseData} from "../models/response-data";
import {HttpOptions} from "./model/http-options";
import {APP_CONFIG, AppConfig} from "../models/app-config";
import {APP_CONFIG_TEST} from "../properties/app-config-test";
import {AppCfgProperties} from "../providers/appConfig.properties";
import {CommonUtil} from "./model/common.util";

@NgModule({
    providers: [
        {provide:APP_CONFIG, useValue:APP_CONFIG_TEST}
    ]
})
@Injectable()
export class HttpWrapper {

    constructor(private http: HttpClient,
                private util: ComponentsUtil,
                private appConfig: AppCfgProperties) {
    }

    /**
     * post请求方法
     * @param {string} url 地址
     * @param data 数据
     * @param {HttpOptions} config 请求配置
     * @returns {Observable<any>}
     */
    post(url: string, data?: any,config: HttpOptions = {}): Observable<any> {
        // 默认显示loading
        config.showLoading = isPresent(config.showLoading) ? config.showLoading : true;
        // 默认显示错误
        config.showError = isPresent(config.showError) ? config.showError : true;
        // 判断服务器响应时间
        config.timeout = isPresent(config.timeout) ? config.timeout : this.appConfig.getInstance().timeout;


        config.showLoading && this.util.showLoading();

        url = this.appConfig.getInstance().server + url;

        data = data || {};

        let request;
        if(config.withSession == "N"){
            request= data;
        }else{
            request = {
                sessionId: CommonUtil.getStoredItem("sessionId"),
                data: data
            }
        }
        console.log(`开始请求：'${url}'，请求数据如下`);
        console.log(request);
        return this.http.post<ResponseData>(url, request, {headers: new HttpHeaders(), observe: 'body'})
            .timeoutWith(config.timeout, Observable.throw('连接超时，请稍后重试'))
            .map((response: ResponseData) => {
                if (config.showLoading) {
                    this.util.hideLoading();
                }
                console.log(`请求：'${url}'获取的数据为：`);
                console.log(response);
                if (response.success) {
                    return response.data;
                } else {
                    throw response.message;
                }
            })
            .catch(err => {
                config.showLoading && this.util.hideLoading();

                if (err instanceof HttpErrorResponse) {
                    console.log(`请求：'${url}'发生异常，异常信息：`);
                    console.log(err);
                    const message = '服务器正在偷懒，请稍候再试';
                    config.showError && this.util.showAlert(message);
                    return Observable.throw(message);
                } else {
                    console.error(`请求：'${url}'返回错误，错误信息：`);
                    console.error(err);
                    config.showError && this.util.showAlert(err);
                    return Observable.throw(err);
                }
            });
    }
}



