import { CommonUtil } from './../../../util/model/common.util';
import { Component } from '@angular/core';
import { NavController,Platform } from 'ionic-angular';
import {Alipay,AlipayOrder} from "@ionic-native/alipay";
import {ChargeService} from "../../../providers/charge.service";
import { Pay, getPayList, OrderDate, CreateOrderResponse} from "./pay.modal";
import { ComponentsUtil } from '../../../util/components.util';

declare let Wechat: any;

@Component({
  selector: 'page-chargenext',
  templateUrl: 'chargenext.html'
})
export class ChargeNextPage {
    amount: number=50;
    coin:number=500;
    isAliPay :boolean=true;   // 充值金额列表
    check:string="font04";
    uncheck:string="font05";
    payList: Pay[] = [];
    classNameFont04="font04";
    classNameFont05="font05";
    //创建订单入参
    orderDate : OrderDate = new OrderDate();
    // 是否选择其他金额
    chooseOtherAmount = false;
    // 其他金额值
    otherAmount: number;

    constructor(public navCtrl: NavController,private cmptUtil:ComponentsUtil, private platform: Platform, private  chargeService: ChargeService, private alipay: Alipay) {
        this.payList=getPayList();
      
    }
    checkPayMod(payModel:String){
        if(payModel=='alipay'){
            this.isAliPay=true;
            this.classNameFont04="font04";
            this.classNameFont05="font05";

        }else{
            this.isAliPay=false;
            this.classNameFont04="font05";
            this.classNameFont05="font04";

        }
    }
    checkCharge(i:number){
        for(let j=0;j<this.payList.length;j++){
            this.payList[j].isChecked=false; 
        }
        this.payList[i].isChecked=true;
        this.amount=this.payList[i].money;
        this.coin=this.amount*10;
        this.chooseOtherAmount=false;
    }
    otherCharge(e){
            for(let j=0;j<this.payList.length;j++){
                this.payList[j].isChecked=false; 
            }
            this.chooseOtherAmount=true;
            if(Number.isInteger(this.otherAmount)){
                if(this.otherAmount>5000){
                    this.otherAmount=5000;  
                }
                this.amount=this.otherAmount;
                this.coin=this.otherAmount*10;
            }else{
                this.otherAmount=null;  
                this.amount=this.otherAmount;
                this.coin=null;
            }  
    }
    
    //支付代码

    beginPay(){
        
        if (!this.amount) {
            this.cmptUtil.showAlert("请选择充值金额，或输入自定义金额！");
            return;
        }
      
        //this.cmptUtil.showLoading('充值中...');
        if (!this.isAliPay) {
            // 微信支付
            this.checkWechat();
        } else {
            // 支付宝支付
            this.createOrder();
        }

    }
      // 判断是否已安装微信插件
    private checkWechat(): void {
        Wechat.isInstalled(isInstalled => {
            if (isInstalled) {
                // 已安装微信
                this.createOrder();
            } else {
                // 未安装微信
                const MSG = '尚未安装微信，无法调起微信支付！';
                console.log(MSG);
                this.cmptUtil.showAlert(MSG);
            }
        }, error => {
            const MSG = '调用微信检查插件失败！';
            this.cmptUtil.hideLoading();
            this.cmptUtil.showAlert(MSG);
            console.log(`${MSG}失败信息：`);
            console.log(error);
        })
    }
  // 创建订单
    private createOrder(): void {
        this.orderDate.payMode = this.isAliPay?'02':'01';//01-微信，02支付宝
        this.orderDate.rechAmount = this.amount;
        this.orderDate.rechType = '01'//账户充值
        this.orderDate.sysType = this.platform.is("android")?'01':"02";//系统类型，01-安卓，02-iOS，03-WEB，04-微信

         var  cor=new CreateOrderResponse();
         cor.rechId="1111111";
         cor.alipayOrderStr='222222';
         cor.wechatOrder={test:"test"};
         /*   */
        this.payWithApp(cor);
        // 创建订单
        // this.chargeService.newOrder(this.orderDate).subscribe(value => {
        //     console.log(value);
        //     console.log(`创建订单成功！订单号：${value.rechId}`);
        //   this.payWithApp(value);
        // }, () => this.cmptUtil.hideLoading());
    }
     // 调用支付应用支付
     private payWithApp(data: CreateOrderResponse): void {
        let onPaySuccess = payResult => {
            let tradeNo;

            console.log('调用应用成功！返回数据：');
            console.log(payResult);

            this.cmptUtil.hideLoading();

            // 判断支付宝返回状态
            if (this.isAliPay && payResult.hasOwnProperty('resultStatus')) {
                switch (payResult.resultStatus) {
                    case '9000':
                        this.cmptUtil.showToast({message: '订单支付成功！'});
                        break;
                    case '8000':
                        this.cmptUtil.showToast({message: '订单处理中...'});
                        break;
                    case '4000':
                        this.cmptUtil.showAlert('订单支付失败！');
                        return;
                    case '6001':
                        this.cmptUtil.showToast({message: '已取消支付'});
                        return;
                    case '6002':
                        this.cmptUtil.showAlert('网络连接出错！');
                        return;
                    default:
                        this.cmptUtil.showAlert('未知支付状态！');
                        return;
                }

                // 获取支付宝订单流水号
                const ORDER_RESULT = JSON.parse(payResult.result);
                tradeNo = ORDER_RESULT.alipay_trade_app_pay_response.trade_no;
                console.log('获取到的支付宝订单流水号：' + tradeNo);
            }

            this.cmptUtil.showLoading('更新订单中...');

            // 更新订单
            // this.rechargeService.updateRechargeOrder(data.rechId, tradeNo)
            // .subscribe(() => {
            //     console.log('充值成功！');
            //     // 变更余额
            //     this.balance = Number((this.balance + this.rechAmount).toFixed(2));
            //     this.cmptUtil.showToast({message: '充值成功！'});
            //     this.cmptUtil.hideLoading();
            // }, () => this.cmptUtil.hideLoading());
        };

        let onPayFailed = error => {
            const CANCEL_MSG = '用户点击取消并返回';
            const MSG = '调用应用支付失败！';
            if (error == CANCEL_MSG) {
                this.cmptUtil.showToast({message: CANCEL_MSG});
            } else {
                this.cmptUtil.showAlert(MSG);
            }
            this.cmptUtil.hideLoading();
            console.log(`${MSG}失败信息：`);
            console.log(error);
        };

        if (this.isAliPay) {
            // 支付宝支付
            console.log('调用支付宝应用支付...');
            this.alipay.pay(data.alipayOrderStr)
            .then(onPaySuccess).catch(onPayFailed);
        } else {
            // 微信支付
            console.log('调用微信应用支付...');
            Wechat.sendPaymentRequest(data.wechatOrder, onPaySuccess, onPayFailed);
        }
    }
}



