import {Component} from '@angular/core';
import {IonicPage, NavController} from 'ionic-angular';
import {HomePage} from "../home/home";
import {LoginPage} from "../login/login";
import {LoginService} from "../../providers/login.service";
import {ComponentsUtil} from "../../util/components.util";
import {Storage} from "@ionic/storage";

/**
 * Generated class for the DirectiveDemoPage page.
 *
 * See http://ionicframework.com/docs/components/#navigation for more info
 * on Ionic pages and navigation.
 */
@IonicPage()
@Component({
    selector: 'page-welcome',
    templateUrl: 'welcome.html',
})
export class WelcomePage {
    countdownNum: number;
    slides = [
        {
            image: "assets/imgs/img02.png",
            last: true
        }
    ];

    constructor(public navCtrl: NavController,
                private util: ComponentsUtil,
                private loginService: LoginService,
                private storage: Storage) {
        this.countDown();
    }

    //获取验证码
    countDown() {
        this.countdownNum = 500;

        let inter = setInterval(() => {
            if (this.countdownNum) {
                --this.countdownNum;
            }else{
                clearInterval(inter);
                this.skip();
            }
        }, 1000)
    }

    skip() {
        this.storage.set("isFirstEntry", true);
        this.loginService.reLogin().subscribe(e => {
            this.navCtrl.setRoot(HomePage);
        }, err => {
            if(err != "AUTO_LOGIN_FAIL"){
                this.util.showAlert(err);
            }
            this.navCtrl.push(LoginPage);
        });
    }

}



