import { ReleaseHistoryPage } from './../pages/task/taskList/release_history';
import { SharePage } from './../pages/share/share';
import {Component, ViewChild} from '@angular/core';
import {App, MenuController, Nav, Platform, ToastController} from 'ionic-angular';
import {StatusBar} from '@ionic-native/status-bar';
import {SplashScreen} from '@ionic-native/splash-screen';
import {HomePage} from "../pages/home/home";
import {FriendPage} from "../pages/friend/friend";
import {Storage} from "@ionic/storage";
import {LoginService} from "../providers/login.service";
import {LoginPage} from "../pages/login/login";
import {ComponentsUtil} from "../util/components.util";
import {WelcomePage} from "../pages/welcome/welcome";
import {MyPage} from "../pages/my/my";
import {ChargePage} from '../pages/charge/charge';
import {ExchangePage} from '../pages/exchange/exchange';
import {CachePage} from '../pages/cache/cache';
import {PassWordPage} from '../pages/password/password';
import {LogoutPage} from "../pages/logout/logout";
import { AppUpdate } from '@ionic-native/app-update';

@Component({
  templateUrl: 'app.html'
})
export class MyApp {
    @ViewChild(Nav) nav: Nav;
    rootPage:any;
    registerBackButton: boolean;
    checkPage;
    pages: Array<{title: string, component: any}>;

    constructor(public platform: Platform,
                public statusBar: StatusBar,
                public splashScreen: SplashScreen,
                private app: App,
                private storage: Storage,
                private toastCtrl: ToastController,
                private menuCtrl: MenuController,
                private util: ComponentsUtil,
                private loginService: LoginService,
                public comUtil: ComponentsUtil,
                public appUpdate:AppUpdate) {

        this.isNotFirstEntry().then(e => {
            this.loginService.reLogin().subscribe(e => {
                this.rootPage = HomePage;
            }, err => {
                if(err != "AUTO_LOGIN_FAIL"){
                    this.util.showAlert(err);
                }
                this.rootPage = LoginPage;
            });
        }, e => {
            this.rootPage = WelcomePage;
        });

        this.initializeApp();

       
        if(this.platform.is("android")){
            this.pages = [
                {title: '首   页', component: HomePage},
                {title: '个人信息', component: MyPage},
                {title: '充值中心', component: ChargePage},
                {title: '兑换中心', component: ExchangePage},
                {title: '我的好友', component: FriendPage},
              //  {title: '系统消息', component: SystemPage},
                {title: '在线升级', component: FriendPage},
                {title: '清除缓存', component: CachePage},
                {title: '密码修改', component: PassWordPage}
            ];
        }else if(this.platform.is("iso")){
            this.pages = [
                {title: '首   页', component: HomePage},
                {title: '个人信息', component: MyPage},
                {title: '充值中心', component: ChargePage},
                {title: '兑换中心', component: ExchangePage},
                {title: '我的好友', component: FriendPage},
              //  {title: '系统消息', component: SystemPage},
                // {title: '在线升级', component: UpdatePage},
                {title: '清除缓存', component: CachePage},
                {title: '密码修改', component: PassWordPage}
            ];
        }else{
            this.pages = [
                {title: '首   页', component: HomePage},
                {title: '个人信息', component: MyPage},
                {title: '充值中心', component: ChargePage},
                {title: '兑换中心', component: ExchangePage},
                {title: '我的好友', component: FriendPage},
              //  {title: '系统消息', component: SystemPage},
                //  {title: '在线升级', component: FriendPage},
                {title: '清除缓存', component: CachePage},
                {title: '密码修改', component: PassWordPage}
            ];
        }
        
    }

    initializeApp() {
        this.platform.ready().then(() => {
            // Okay, so the platform is ready and our plugins are available.
            // Here you can do any higher level native things you might need.
            this.statusBar.styleDefault();
            this.splashScreen.hide();
            this.registerBackButtonAction();
          //  this.checkForUpdate();
        });

    }

    openPage(page) {
        // Reset the content nav to have just this page
        // we wouldn't want the back button to show in this scenario
        if(page.title=="在线升级"){
            this.comUtil.showLoading("正在检查更新");
            this.checkForUpdate();
            this.comUtil.hideLoading();
        }else{
            this.nav.setRoot(page.component);
        }
        
    }

    toLogoutPage() {
        this.menuCtrl.close();
        this.nav.setRoot(LogoutPage);
    }
    toSharePage(){
        this.menuCtrl.close();
        this.nav.setRoot(SharePage);
    }
    exitApp() {
        if (this.registerBackButton) {
            this.platform.exitApp();
        } else {
            this.registerBackButton = true;
            this.toastCtrl.create({
                message: "再按一次退出应用",
                duration: 2000,
                position: "bottom",
                cssClass: "toast-black"
            }).present();
            setTimeout(() => this.registerBackButton = false, 2000)
        }
    }

    // 判断当前页面是否首页
    isHomePage() {
        return this.app.getActiveNav().getActive().component ;
    }

    registerBackButtonAction() {
        console.log('监听右键：');
        let that=this;
        this.platform.registerBackButtonAction(() => {
            console.log('监听右键Boolean值：' + that.checkPage);
            if(that.app.getActiveNav().getActive().component === HomePage){
                that.exitApp();
            }else if(that.app.getActiveNav().getActive().component === ReleaseHistoryPage){
                that.nav.setRoot(HomePage);
            }else{
                that.app.goBack();
            }
        }, 101);
        // 后面的数字10是必要参数，如果不写默认是0，数字越大优先级越高
    }

    isNotFirstEntry(): Promise<any> {
        return new Promise((resolve, reject) => {
            let isMobile = this.platform.is("iso") || this.platform.is("android");
            if(!isMobile){
                // 浏览器调试默认非首次，不加载启动动画
                console.log("web端浏览");
                resolve(true);
            }else{
                this.storage.get("isFirstEntry").then(isFirstEntry => {
                    console.log(isFirstEntry ? "非首次进入APP" : "首次进入APP");
                    isFirstEntry ? resolve(true): reject(false);
                }).catch(err=>{
                    console.log('Your SetRadious dont exist: ' + JSON.stringify(err));
                });
            }
        });
    }
    checkForUpdate(){
        try {
            const updateUrl = 'https://www.izile.cn/unionapp.xml';
            this.appUpdate.checkAppUpdate(updateUrl)
            .then(data => {
                console.log(data) ;
                console.log(data.version) ;
                console.log('Update available') 
            })
            .catch(err=>{
                    console.log(err);
            });   
        } catch (error) {
            console.log(error);
        }
        
    }
}



