import {Component} from '@angular/core';
import {IonicPage, NavController, NavParams, ActionSheetController, AlertController} from 'ionic-angular';

import {ImagePicker, ImagePickerOptions} from "@ionic-native/image-picker";
import {Camera, CameraOptions} from "@ionic-native/camera";
import {TaskService} from "../../../providers/task.service";
import {CommonUtil} from "../../../util/model/common.util";
import {ExecutingPage} from "../executing/executing";
import {HomePage} from "../../home/home";
import {CheckPage} from "./check";

@IonicPage()
@Component({
    selector: 'page-upload',
    templateUrl: 'upload.html',
})
export class UploadPage {

    avatar: string = "";
    private task:any;//任务信息
    private path:any;//base64 文件路径
    fileInfo:any={};//文件信息
    img_1:any = "";//正面照片id
    img_2:any = "";//反面照片id
    flag:any;//标志位正面反面

    constructor(public navCtrl: NavController,
                public params: NavParams,
                public actionSheetCtrl: ActionSheetController,
                public taskService: TaskService,
                public alertCtrl: AlertController,
                public imagePicker: ImagePicker,
                public camera: Camera) {
        //this.fileInfo.fileId = "0ec633ed-57a8-4217-83c6-f465605a76ec";
    }

    presentActionSheet(flag) {
        this.flag = flag;
        let actionSheet = this.actionSheetCtrl.create({
            buttons: [{
                text: '拍照',
                role: 'takePhoto',
                handler: () => {
                    this.takePhoto();
                }
            },
            //  {
            //     text: '从相册选择',
            //     role: 'chooseFromAlbum',
            //     handler: () => {
            //         this.chooseFromAlbum();
            //     }
            // },
            {
                text: '取消',
                role: 'cancel',
                handler: () => {
                    console.log("cancel");
                }
            }]
        });

        actionSheet.present().then(value => {
            return value;
        });
    }
    takePhoto() {
        const options: CameraOptions = {
            quality: 100,
            allowEdit: true,
            targetWidth: 400,
            targetHeight: 400,
            saveToPhotoAlbum: true,
            sourceType:this.camera.PictureSourceType.CAMERA,//从相机选择图片
            destinationType: this.camera.DestinationType.DATA_URL
        };

        this.camera.getPicture(options).then(imageData => {
            let base64Image = {'fileType':'jpg','fileBase64':imageData};
            this.path = base64Image;
            this.upload();
            console.log('base64Image: ' + base64Image);
        }, error => {
            console.log('Error: ' + error);
        });
    }

    chooseFromAlbum() {
        const options: CameraOptions = {
            quality: 100,
            allowEdit: true,
            targetWidth: 400,
            targetHeight: 400,
            saveToPhotoAlbum: true,
            sourceType:this.camera.PictureSourceType.SAVEDPHOTOALBUM,//从相册选择图片
            destinationType: this.camera.DestinationType.DATA_URL
        };

        this.camera.getPicture(options).then(imageData => {
            let base64Image = {'fileType':'jpg','fileBase64':imageData};
            this.path = base64Image;
            this.upload();
            console.log('base64Image: ' + base64Image);
        }, error => {
            console.log('Error: ' + error);
        });
    }

    presentAlert() {
        let alert = this.alertCtrl.create({title: "上传失败", message: "只能选择一张图片", buttons: ["确定"]});
        alert.present().then(value => {
            return value;
        });
    }

    upload(){
        this.taskService.uploadFile(this.path).subscribe(data => {
            this.fileInfo = data;
            if(this.flag == "1"){
                this.img_1 = data.fileId;
            }
            if(this.flag == "2"){
                this.img_2 = data.fileId;
            }

        }, CommonUtil.emptyCallback);
    }

    //完成任务
    toHome(){//跳转到主页
        this.navCtrl.push(HomePage);
    }

    //保存身份证照片id
    saveCardImg(){
        if(this.img_1 ==""){
            this.showAlert('正面照片还未选择！');
            return;
        }
        if(this.img_2 ==""){
            this.showAlert('反面照片还未选择！');
            return;
        }
        let imgInfo:any={};
        imgInfo.img_1 = this.img_1;
        imgInfo.img_2 = this.img_2;
        this.taskService.updateCardImg(imgInfo).subscribe(data => {
            this.navCtrl.push(CheckPage);
        }, CommonUtil.emptyCallback);
    }

    //显示提示信息
    showAlert(str:string) {
        let alert = this.alertCtrl.create({
            title: '',
            subTitle: str
        });
        alert.present();
    }

}


