import { MyPicPage } from './mypic/mypic';
import { ComponentsUtil } from './../../util/components.util';
import { MyInfoPage } from './myinfo/myinfo';
import { MyService } from './../../providers/my.service';
import { Component } from '@angular/core';
import { NavController } from 'ionic-angular';
@Component({
  selector: 'page-my',
  templateUrl: 'my.html'
})
export class MyPage {
  areaList:any=[];
  myInfo:any={};
  taskDist:number=0;
  taskLev:number=1;
  areaId:string;
  star1:string="0";
  star2:string="0";
  star3:string="0";
  star4:string="0";
  star5:string="0";
  constructor(public navCtrl: NavController,private myservice:MyService,private componentsUtil:ComponentsUtil) {
  }
  ionViewWillEnter(){
    this.myservice.getAreaList().subscribe(data => {
      this.areaList=data;
      for(let i=0;i<this.areaList.length;i++){
        if(this.areaList[i].AREA_ID==this.areaList[i].AREA){
            this.areaList[i].checked=true;
        }else{
            this.areaList[i].checked=false;
        }
      }
      this.areaId=this.areaList[0].AREA;
    });
    this.myservice.getMyInfo().subscribe(data =>{
      this.myInfo=data;
      this.taskDist=this.myInfo.TASK_DIST;
      this.taskLev=this.myInfo.TASK_LEV;
      this.areaId=this.myInfo.AERA_ID;
      console.log(this.taskLev);
      if(this.taskLev==1){
        this.star1="";
      this.star2="0";
      this.star3="0";
      this.star4="0";
      this.star5="0";
      }
      if(this.taskLev==2){
        this.star1="";
      this.star2="";
      this.star3="0";
      this.star4="0";
      this.star5="0";
      }
      if(this.taskLev==3){
        this.star1="";
      this.star2="";
      this.star3="";
      this.star4="0";
      this.star5="0";
      }
      if(this.taskLev==4){
        this.star1="";
      this.star2="";
      this.star3="";
      this.star4="";
      this.star5="0";
      }
      if(this.taskLev==5){
        this.star1="";
      this.star2="";
      this.star3="";
      this.star4="";
      this.star5="";
      }
      
     
    });
    
  }
 ionViewDidLeave(){
    this.updateTaskInfo();
 }
  updateMyInfo(){
    this.navCtrl.push(MyInfoPage,{"userName":this.myInfo.USER_NAME,"telephone":this.myInfo.TELEPHONE});
  }
  updateTaskInfo(){
    console.log(this.areaId);
    if(this.areaId&&this.taskDist&&this.taskLev){
      this.myservice.updateTaskInfo({taskDist:this.taskDist,taskLev:this.taskLev,areaId:this.areaId}).subscribe(data => {
        if(data){
          // this.toastOptions.message="已保存";
          // this.componentsUtil.showToast(this.toastOptions);
        }
      });
    }
  }
  checkArea(i:string){
    for(let j=0;j<this.areaList.length;j++){
          this.areaList[j].checked=false;
    }
    this.areaList[i].checked=true;
    this.areaId=this.areaList[i].AREA_ID;
    console.log(this.areaList);
    this.updateTaskInfo();
  }
  clickstar(flag:any){
    this.taskLev=flag;
    if(flag==1){
      this.star1="";
      this.star2="0";
      this.star3="0";
      this.star4="0";
      this.star5="0";
    }else if(flag==2){
      this.star1="";
      this.star2="";
      this.star3="0";
      this.star4="0";
      this.star5="0";
    }else if(flag==3){
      this.star1="";
      this.star2="";
      this.star3="";
      this.star4="0";
      this.star5="0";
    }else if(flag==4){
      this.star1="";
      this.star2="";
      this.star3="";
      this.star4="";
      this.star5="0";
    }else if(flag==5){
      this.star1="";
      this.star2="";
      this.star3="";
      this.star4="";
      this.star5="";
    }
    this.updateTaskInfo();
  }
  changeTaskDist(){
    this.updateTaskInfo();
  }
  updateMyPic(){
    this.navCtrl.push(MyPicPage,{"PIC_ID":this.myInfo.PIC_ID});
  }
}



