import { Component } from '@angular/core';
import { NavController } from 'ionic-angular';
import { ComponentsUtil } from './../../../util/components.util';
import { ExchangeService } from './../../../providers/exchange.service';

@Component({
  selector: 'page-exchangetelefare',
  templateUrl: 'exchangetelefare.html'
})
export class ExchangeTelefarePage {

  account:string;
  coin:number=0;
  exchangeCoin:number=0;
  exchangeObj:any=0;
  checkTel:boolean=true;
  constructor(public navCtrl: NavController,private exchangeService:ExchangeService,private comutil:ComponentsUtil) {
    
  }
  ionViewDidEnter(){
    this.exchangeService.getCoinInfo().subscribe(data=>{
      this.coin=data;
    });
    
  }
  exchange(){
    if(!this.checkTel){
      this.comutil.showAlert("请输入正确的手机号");
      return;
    }
    if(!this.exchangeCoin||this.exchangeCoin==null||this.exchangeCoin==0){
      this.comutil.showAlert("请选择正确的兑换金额");
      return;
    }
    this.exchangeObj=(this.exchangeCoin/10).toFixed(0);
    let obj={account:this.account,exchangeObj:this.exchangeObj};
    this.exchangeService.exchangeTelefare(obj).subscribe(data => {
      if(data){
        this.comutil.showAlert("兑换提交成功");
        this.exchangeService.getCoinInfo().subscribe(data=>{
          this.coin=data;
        });
        this.exchangeCoin=0;
        this.exchangeObj=0;
      }else{
        this.comutil.showAlert("兑换提交失败");
        this.exchangeService.getCoinInfo().subscribe(data=>{
          this.coin=data;
        });
        this.exchangeCoin=0;
        this.exchangeObj=0;
      }
    });
  }
  checkMobile(e) {
    console.log(e);  
    let str=e.target.value;
    if(str==""){  
      this.checkTel= false;  
    }  
    else{  
        var re = /^1\d{10}$/                           
        if (re.test(str)) {  
          this.checkTel= true; 
        } else {  
          this.checkTel= false;
        }  
    }  
}  

}



