import {Component, Injector} from '@angular/core';
import {ModalController, NavController} from 'ionic-angular';
import {LoginService} from "../../providers/login.service";
import {HomePage} from "../home/home";
import {APP_CONFIG, AppConfig} from "../../models/app-config";
import {ForgetPwdPage} from "./forget-pwd/forget-pwd";
import {AppCfgProperties} from "../../providers/appConfig.properties";
import {RegisterPage} from "./register/register";
import {CommonUtil} from "../../util/model/common.util";
import {UserService} from "../../providers/user.service";
import {FriendPage} from "../friend/friend";

@Component({
    selector: 'page-login',
    templateUrl: 'login.html'
})
export class LoginPage {

    private static count: number = 5;

    countdownNum: number;
    isSmsLogin: boolean = false;
    userName: string;
    smsCode: string;
    password: string;

    constructor(public navCtrl: NavController,
                private appConfig: AppCfgProperties,
                private loginService: LoginService,
                private userService: UserService) {

    }

    sendLoginSms() {
        this.loginService.sendSms(this.userName).subscribe(data => {
            this.smsCountDown();
        }, CommonUtil.emptyCallback);
    }

    userNameValid(): boolean{
        return this.userName && (this.userName.length == 11 || this.userName.length == 18);
    }

    doLogin() {
        const DATA = {
            isSmsLogin: this.isSmsLogin,
            loginPassword: this.password,
            userName: this.userName + "",
            smsCode: this.smsCode + ""
        };
        this.loginService.doLogin(DATA).subscribe(val => {
            this.userService.loginUesr(val);
            this.navCtrl.setRoot(HomePage);
        }, CommonUtil.emptyCallback);
    }

    //获取验证码
    smsCountDown() {
        this.countdownNum = this.appConfig.getInstance().smsInterval;

        let inter = setInterval(() => {
            if (this.countdownNum) {
                --this.countdownNum;
            }else{
                clearInterval(inter);
            }
        }, 1000)
    }

    toForgetPwd() {
        this.navCtrl.push(ForgetPwdPage, {userName: this.userName});
    }

    toRegister() {
        this.navCtrl.push(RegisterPage, {userName: this.userName});
    }

    smsLogin(){
        this.isSmsLogin = true;
    }

}



