//支付金额
export class Pay {
    //是否选中
    isChecked: boolean = false;
    //支付金额
    money: number;
}

//订单入参
 export class OrderDate {
     //支付方式
     payMode:string;
     //充值金额
     rechAmount : number;
     //充值类型，01-账户充值,02-包月
     rechType : string;
     //系统类型，01-安卓，02-iOS，03-WEB，04-微信
     sysType :string;
 }

 export class CreateOrderResponse {
    // 唯一标识充值id（数字编码）
    rechId: string;
    // 支付宝app支付请求参数，在payMode="02"时返回
    alipayOrderStr: string;
    // 微信app支付请求参数
    wechatOrder: object;
}

// 获取
export function getPayList(moneyList = [50, 100, 200, 300, 500, 1000]): Pay[] {
    let payList: Pay[] = [];

    for (let i = 0; i < moneyList.length; i++) {
        let checked;
        if(i==0){
            checked= true;
        }else{
            checked=false;
        }
        payList.push({

            isChecked:checked,
            money: moneyList[i]
        });
    }

    return payList;
}



