export class GlobalVariable {
    private static isFirstFlag: any = true;
    private static city:any = "";

    //获取全局变量的值
    public static getFirstFlag() {
        return this.isFirstFlag;
    }

    //设置全局变量的值
    public static setFirstFlag(bool) {
        return this.isFirstFlag = bool;
    }

    public static getCity() {
        return this.city;
    }

    //设置全局变量的值
    public static setCity(city) {
        return this.city = city;
    }
}


