import { Component } from '@angular/core';
import { NavController, NavParams, AlertController } from 'ionic-angular';
import { TaskService} from "../../../providers/task.service";
import {CommonUtil} from "../../../util/model/common.util";
import {AlreadyAcceptPage} from "../accept/already_accept";
import {ExecutingPage} from "../executing/executing";
import {FinishHistoryPage} from "../taskList/finish_history";
import { CallNumber } from '@ionic-native/call-number';
import {DbDict} from "../../../util/DbDict";
import {ComponentsUtil} from "../../../util/components.util";

@Component({
    selector: 'page-already_detail',
    templateUrl: 'already_detail.html'
})
export class AlreadyDetailPage {
    picId: string;
    private task:any;

    constructor(public navCtrl: NavController,
                private param:NavParams,
                private componentsUtil: ComponentsUtil,
                private taskService: TaskService,
                private callNumber: CallNumber,
            private alertCtrl:AlertController) {
        this.task = param.get("task");
        
        //标准代码转中文描述
        this.task.TYPE_STR = DbDict.code2type(this.task.TYPE, "TASK_TYPE");
        this.task.BIG_TYPE_STR = DbDict.code2type(this.task.BIG_TYPE, "TASK_BIG_TYPE");
        this.task.FEE_TYPE_STR = DbDict.code2type(this.task.FEE_TYPE, "TASK_FEE_TYPE_REVERSE");
        this.task.PARTNER_GENDER_STR = DbDict.code2type(this.task.PARTNER_GENDER, "TASK_PARTNER_GENDER");
        this.timeParse();
    }

    //完成任务【跳转到已完成任务列表】
    finishTask(){
        this.taskService.finishTask(this.task).subscribe(data => {
            this.navCtrl.push(FinishHistoryPage,{task:this.task});
        }, CommonUtil.emptyCallback);
    }

    exitTask(taskId){
        this.taskService.exitTask(taskId).subscribe(data => {
            this.navCtrl.pop();
        }, CommonUtil.emptyCallback);
    }

    //联系发布人
    call(phone){
        console.log(phone);
        let that=this;
        const alert = this.alertCtrl.create({
            title: "拨打:"+phone+"?",
            message: "",
            buttons: [
                {
                    text: '取消',
                    role: 'cancel',
                    handler:  () =>{
                        console.log('选择取消按钮');
                    }
                }, {
                    text:  '确认',
                    handler:   () =>{
                        that.callNumber.callNumber(phone, true)
                            .then(res => console.log('Launched dialer!', res))
                            .catch(err => console.log('Error launching dialer', err));
                    }
                }
            ]
        }).present();
        
        
    }

    getTime(): string {
        let date = new Date(this.task.SUBSCRIBE_TIME.replace("-", "/"));
        let time = CommonUtil.time2ZhDay(date) + "/" + date.getHours() + ":" + date.getMinutes();
        return time;
    }

    timeParse() {
        let EXPIRE_TIME = new Date(this.task.EXPIRE_TIME);
        let mins = CommonUtil.diff(EXPIRE_TIME, new Date(), CommonUtil.MINUTES);
        console.log(mins);
        this.task.EXPIRE_TIME_STR = CommonUtil.min2ZhHourMin(mins);
    }


    answerTask(){

        if(!this.task.answer){
            this.componentsUtil.showAlert("请填写问题答案");
            return false;
        }

        let data = {
            taskId: this.task.ID,
            answer: this.task.answer
        };

        this.taskService.answerTask(data).subscribe(data => {
            this.navCtrl.pop();
        }, CommonUtil.emptyCallback);
    }

}



