import { Component } from '@angular/core';
import {AlertController, NavController, NavParams} from 'ionic-angular';
import { TaskService} from "../../../providers/task.service";
import {CommonUtil} from "../../../util/model/common.util";
import {HomePage} from "../../home/home";
import {DbDict} from "../../../util/DbDict";
import {ComponentsUtil} from "../../../util/components.util";
import {ConfirmPublisherPage} from "./confirm_publisher";
import {ReleasePage_2} from "../release/release_2";
import {ExecutingPage} from "../executing/executing";
import {ConfirmPublisherCheckUnFinishPage} from "./confirm_publisher_check_un_finish";

@Component({
    selector: 'page-publisher-check',
    templateUrl: 'confirm_publisher_check.html'
})
export class ConfirmPublisherCheckPage {
    picId: string;
    task:any={};
    level:any;
    state: string = '04';

    constructor(public navCtrl: NavController,
                private param:NavParams,
                public alertCtrl: AlertController,
                public componentsUtil: ComponentsUtil,
                private taskService: TaskService) {
        this.task = param.get("task");
        //标准代码转中文描述
        this.task.TYPE_STR = DbDict.code2type(this.task.TYPE, "TASK_TYPE");
        this.task.BIG_TYPE_STR = DbDict.code2type(this.task.BIG_TYPE, "TASK_BIG_TYPE");
        this.task.FEE_TYPE_STR = DbDict.code2type(this.task.FEE_TYPE, "TASK_FEE_TYPE");
        this.task.PARTNER_GENDER_STR = DbDict.code2type(this.task.PARTNER_GENDER, "TASK_PARTNER_GENDER");
        this.timeParse();
    }

    //重发任务
    againTask(id:string){
        this.task.again = true;
        this.navCtrl.push(ReleasePage_2,{taskInfo:this.task});
    }

    //跳转到评价页面
    toEval(){
        this.navCtrl.push(ConfirmPublisherPage,{task:this.task});
    }
    confirmTask(){
        let data = {
            taskId: this.task.ID,
            state: this.state
        }

        if(this.state == '04'){
            this.taskService.confirmFinishTask({taskId: this.task.ID}).subscribe(data => {
                this.navCtrl.push(ConfirmPublisherPage,{task: this.task});
            }, CommonUtil.emptyCallback);
        }else{
            this.taskService.confirmUnFinishTask({taskId: this.task.ID}).subscribe(data => {
                this.navCtrl.push(ConfirmPublisherCheckUnFinishPage,{task: this.task});
            }, CommonUtil.emptyCallback);
        }
        // this.taskService.confirmTask(data).subscribe(data => {
        //     this.navCtrl.push(HomePage);
        // }, CommonUtil.emptyCallback);
    }

    timeParse() {
        let startTime = new Date(this.task.ORDER_TIME);
        let endTime = new Date(this.task.FINISH_TIME);
        let mins = CommonUtil.diff(endTime, startTime, CommonUtil.MINUTES);
        console.log(mins);
        this.task.EXPIRE_TIME_STR = CommonUtil.min2ZhHourMin(mins);
        this.task.TOTAL_TIME_STR = this.totalTimeParse(new Date(this.task.FINISH_TIME), new Date(this.task.ORDER_TIME));
    }

    totalTimeParse(finishTime, startTime) {
        let diffs = CommonUtil.diff(finishTime, startTime, CommonUtil.SECONDS);

        let hours = Math.floor(diffs / 3600);
        let mins =  Math.floor((diffs - hours * 3600) / 60);
        let seconds = diffs - hours * 3600 - mins * 60;
        return CommonUtil.time2DoubleDigit(hours) + ":" + CommonUtil.time2DoubleDigit(mins) + "' " + seconds;
    }
}



