import { Component } from '@angular/core';
import {AlertController, NavController} from 'ionic-angular';
import { TaskService} from "../../../providers/task.service";
import {ReleasePage_2} from "./release_2";
import {ReleaseHistoryPage} from "../taskList/release_history";

@Component({
    selector: 'page-release',
    templateUrl: 'release.html'
})
export class ReleasePage {
    title: string;
    taskInfo: any = {};

    constructor(public navCtrl: NavController,
                public alertCtrl: AlertController,
                private taskService: TaskService) {
    }

    releaseNext(str:string){//跳转到发布任务下一步   && this.title == "" || this.title == null
        if(this.title == "" || this.title == null){
            this.showAlert('请填写任务标题');
            return;
        }
        this.taskInfo.title = this.title;
        this.navCtrl.push(ReleasePage_2,{taskInfo:this.taskInfo});
    }

    //显示提示信息
    showAlert(str:string) {
        let alert = this.alertCtrl.create({
            title: '',
            subTitle: str
        });
        alert.present();
    }

    //跳转到已发布的
    toReleaseHistory(){
        this.navCtrl.push(ReleaseHistoryPage);
    }

}



