import { CommonUtil } from './../../../util/model/common.util';
import { ComponentsUtil } from './../../../util/components.util';
import { MyService } from './../../../providers/my.service';
import { Component } from '@angular/core';
import { NavController,NavParams } from 'ionic-angular';

@Component({
  selector: 'page-myinfo',
  templateUrl: 'myinfo.html'
})
export class MyInfoPage {
  telephone:number;
  userName:string;
  constructor(public navCtrl: NavController,private navparams:NavParams,private myService:MyService,private componentsUtil:ComponentsUtil) {
    this.telephone=this.navparams.data.telephone;
    this.userName=this.navparams.data.userName;
    
  }
  updateMyInfo(){
    this.myService.updateMyInfo({telephone:this.telephone,userName:this.userName}).subscribe(data =>{
        if(data){
          this.componentsUtil.showAlert("保存成功");
          this.navCtrl.pop();
        }
    });
  }
}



