import { Component } from '@angular/core';
import { NavController } from 'ionic-angular';
import { TaskService} from "../../../providers/task.service";
import {CommonUtil} from "../../../util/model/common.util";
import {TaskListPage} from "../taskList/taskList";

@Component({
    selector: 'page-query',
    templateUrl: 'query.html'
})
export class QueryPage {
    stations: Array<any>;
    picId: string;

    constructor(public navCtrl: NavController,
                private taskService: TaskService) {
    }

    //查询可接任务，状态 【STATE = '01'】
    getTaskList(type:string) {
        if(type == "01"){//跑腿任务
            let param :any={};
            param.type = type;//查询任务的类型
            param.state = '01';//查询可接任务，状态 【STATE = '01'】
            this.taskService.getTaskList(param).subscribe(data => {

                this.navCtrl.push(TaskListPage,{taskArr: data});

            }, CommonUtil.emptyCallback);
        }
        if(type == '02'){//美食任务
            this.navCtrl.push(TaskListPage);
        }
        if(type == '03'){//日常任务
            this.navCtrl.push(TaskListPage);
        }
    }

}



