import { Component } from '@angular/core';
import {NavController, NavParams} from 'ionic-angular';
import { TaskService} from "../../../providers/task.service";
import {DetailPage} from "../detail/detail";
import {CommonUtil} from "../../../util/model/common.util";
import {FinishDetailPage} from "../detail/finish_detail";
import {DbDict} from "../../../util/DbDict";

@Component({
    selector: 'page-finish_history',
    templateUrl: 'finish_history.html'
})
export class FinishHistoryPage {
    picId: string;
    public taskList: Array<any>;

    constructor(public navCtrl: NavController,
                private param:NavParams,
                private taskService: TaskService) {
        this.taskList = param.get("taskArr");
        this.getAlreadyFinish("");
    }

    public showDetail(id:string){//展示任务明细
        for (let task of this.taskList) {
            if(id == task.ID){
                this.navCtrl.push(FinishDetailPage,{task: task});
            }
        }

    }


    //获取已完成列表
    public getAlreadyFinish(classType:string){
        if(classType == undefined || classType == null || classType == ""){
            classType = "10";
        }
        this.taskService.getAlreadyFinish(classType).subscribe(data => {
            this.taskList = data;
            for(let i=0;i < this.taskList.length;i++){
                //任务分类大类型
                this.taskList[i].BIG_TYPE_STR = DbDict.code2type(this.taskList[i].BIG_TYPE, "TASK_BIG_TYPE");
            }
        }, CommonUtil.emptyCallback);
    }
}



