import { Component } from '@angular/core';
import {NavController, NavParams} from 'ionic-angular';
import { TaskService} from "../../../providers/task.service";
import {CommonUtil} from "../../../util/model/common.util";
import {DetailPage} from "../detail/detail";
import {DbDict} from "../../../util/DbDict";

@Component({
    selector: 'page-taskList',
    templateUrl: 'taskList.html'
})
export class TaskListPage {
    picId: string;
    public taskList: Array<any>;
    type:any;
    type_2:any="";//下级类型
    level:any="";//任务等级
    order :any="";//排序方式

    constructor(public navCtrl: NavController,
                private param:NavParams,
                private taskService: TaskService) {
        this.type = param.get("type");

    }

    ionViewWillEnter() {
        this.getTaskList();
    }

    getTaskList(){
        let param = {
            state: '01',//查询可接任务，状态 【STATE = '01'】
            type: this.type
        }
        this.taskService.getTaskList(param).subscribe(data => {
            this.taskList = data;
            for(let i=0;i < this.taskList.length;i++){
                //任务分类大类型
                this.taskList[i].BIG_TYPE_STR = DbDict.code2type(this.taskList[i].BIG_TYPE, "TASK_BIG_TYPE");
            }
        }, CommonUtil.emptyCallback);
    }

    public showDetail(task){//展示任务明细
        this.navCtrl.push(DetailPage,{task: task});
    }

    //查询
    queryTask(){
        let filter :any = {};
        filter.type = this.type+"";
        filter.level = this.level;
        filter.type_2 = this.type_2;
        filter.order = this.order+"";
        filter.state = '01';//查询可接任务，状态 【STATE = '01'】
        this.taskService.getTaskForFilter(filter).subscribe(data => {
            this.taskList = data;//展示数据
            for(let i=0;i < data.length;i++){
                //任务分类大类型
                this.taskList[i].BIG_TYPE_STR = DbDict.code2type(this.taskList[i].BIG_TYPE, "TASK_BIG_TYPE");
            }
        }, CommonUtil.emptyCallback);
    }

    switchType_1(){this.queryTask();}
    switchType_2(){this.queryTask();}
    switchType_3(){
        this.queryTask();
    }
}



