import { ExchangeService } from './../../../providers/exchange.service';
import { Component } from '@angular/core';
import { NavController } from 'ionic-angular';
import { ComponentsUtil } from './../../../util/components.util';

@Component({
  selector: 'page-exchangeprop',
  templateUrl: 'exchangeprop.html'
})
export class ExchangePropPage {

  coin:number=0;
  step:number;
  hornL:number;
  hornH:number;
  exchangeCoin:number=0;
  exchangeObj:any=0;
  constructor(public navCtrl: NavController,private exchangeService:ExchangeService,private comutil:ComponentsUtil) {
  }
  ionViewDidEnter(){
    this.exchangeService.getCoinInfo().subscribe(data=>{
      this.coin=data;
    });
    this.exchangeService.getConfig("HORN_L").subscribe(data=>{
      this.hornL=data[0].CONFIG_VALUE;
      this.step=this.hornL;
    });
    this.exchangeService.getConfig("HORN_H").subscribe(data=>{
      this.hornH=data[0].CONFIG_VALUE;
    });
  }
  exchange(){

    if(this.step==null){
      this.comutil.showAlert("请选择兑换道具");
      return;
    }
    if(!this.exchangeCoin||this.exchangeCoin==null||this.exchangeCoin==0){
      this.comutil.showAlert("请选择正确的兑换金额");
      return;
    }
    this.exchangeObj=(this.exchangeCoin/this.step).toFixed(0);
    var hornType;
    if(this.step==this.hornL){
      hornType='hornL'
    }else if(this.step==this.hornH){
      hornType='hornH'
    }
    let obj={account:this.step,exchangeObj:this.exchangeObj,hornType:hornType};
    this.exchangeService.exchangeProp(obj).subscribe(data => {
      if(data){
        this.comutil.showAlert("兑换成功");
        this.exchangeService.getCoinInfo().subscribe(data=>{
          this.coin=data;
        });
        this.exchangeCoin=0;
        this.exchangeObj=0;

      }else{
        this.comutil.showAlert("兑换失败");
        this.exchangeService.getCoinInfo().subscribe(data=>{
          this.coin=data;
        });
        this.exchangeCoin=0;
        this.exchangeObj=0;
      }
    });
  }
  checkProp(flag:string){
    if(flag=='1'){
      this.step=this.hornL;
    }else  if(flag=='2'){
      this.step=this.hornH;
    }
  }

}



