import { ComponentsUtil } from './../../../util/components.util';
import { ExchangeService } from './../../../providers/exchange.service';
import { Component } from '@angular/core';
import { NavController } from 'ionic-angular';

@Component({
  selector: 'page-exchangemoney',
  templateUrl: 'exchangemoney.html'
})
export class ExchangeMoneyPage {
  account:string;
  coin:number=0;
  exchangeCoin:number=0;
  exchangeObj:any=0;
  constructor(public navCtrl: NavController,private exchangeService:ExchangeService,private comutil:ComponentsUtil) {
    
  }
  ionViewDidEnter(){
    this.exchangeService.getCoinInfo().subscribe(data=>{
      this.coin=data;
    });
  }
  exchange(){
    if(!this.account||this.account==null||this.account==''){
      this.comutil.showAlert("请输入正确的支付宝账号");
      return;
    }
    if(!this.exchangeCoin||this.exchangeCoin==null||this.exchangeCoin==0){
      this.comutil.showAlert("请选择正确的兑换金额");
      return;
    }
    this.exchangeObj=(this.exchangeCoin/10).toFixed(0);
    let obj={account:this.account,exchangeObj:this.exchangeObj};
    this.exchangeService.exchangeMoney(obj).subscribe(data => {
      if(data){
        this.comutil.showAlert("兑换提交成功");
        this.exchangeService.getCoinInfo().subscribe(data=>{
          this.coin=data;
        });
        this.exchangeCoin=0;
        this.exchangeObj=0;
      }else{
        this.comutil.showAlert("兑换提交失败");
        this.exchangeService.getCoinInfo().subscribe(data=>{
          this.coin=data;
        });
        this.exchangeCoin=0;
        this.exchangeObj=0;
      }
    });
  }

}



