import { UnFriendInfoPage } from './unfriendinfo/unfriendinfo';
import { FriendService } from './../../providers/friend.service';
import { Component } from '@angular/core';
import { IonicPage, NavController, NavParams, Icon } from 'ionic-angular';
import {UserService} from "../../providers/user.service";
import{FriendInfoPage} from "./friendinfo/friendinfo";



@Component({
  selector: 'page-friend',
  templateUrl: 'friend.html',
})
export class FriendPage {
  friends:any=[];
  unFriends:any=[];
  input="";
    tabs: string = "01";
  constructor(public navCtrl: NavController,private userService: UserService,private friendService:FriendService, private param:NavParams) {

  }

  ionViewDidEnter() {
    this.getFriendList();
    //this.getUnFriendList();
  }
  getFriendList(){
    this.friendService.getFriendList(this.input).subscribe(data => {
        this.friends=data;
    });
  }
  getUnFriendList(){
    this.friendService.getUnFriendList(this.input).subscribe(data => {
        this.unFriends=data;
    });
  }
  getFriendInfo(friend:any){
      
      let id;
      if(friend.FRIEND_ID){
        id=friend.FRIEND_ID;
      }
      if(friend.USER_ID){
        id=friend.USER_ID;
      }
     this.friendService.getFriendInfo(id).subscribe(data => {
         if(data){
            if(this.tabs=="01"){
                this.navCtrl.push(FriendInfoPage,{friendInfo:data});
            }else if(this.tabs=="02"){
                this.navCtrl.push(UnFriendInfoPage,{friendInfo:data});
            }
        }
      });
  }

  removeFriend(friend:any,index:number){
      this.friendService.removeFriend(friend.USER_ID).subscribe(data => {
          if(data){
              this.friends.splice(index,1);
          }
      });
  }
  getInput(e){
      this.input=e.target.value;
      if(e.keyCode==13){
          if(this.tabs=="01"){
            this.friendService.getFriendList(this.input).subscribe(data => {
                this.friends=data;
            });
          }else if(this.tabs='02'){
            this.friendService.getUnFriendList(this.input).subscribe(data => {
                this.unFriends=data;
            });
          }
          
      }

  }
  segmentChanged(e){
    if(e._value=='01'){
        this.getFriendList();
    }else if(e._value=='02'){
        this.getUnFriendList();
    }
  }

}



