import { MyPage } from './../my';
import { MyService } from './../../../providers/my.service';
import { Component } from '@angular/core';
import { NavController, AlertController, NavParams } from 'ionic-angular';
import {ImagePicker, ImagePickerOptions} from "@ionic-native/image-picker";
import {Camera, CameraOptions} from "@ionic-native/camera";
@Component({
  selector: 'page-mypic',
  templateUrl: 'mypic.html'
})
export class MyPicPage {
  avatar:any;
  path:any;
  fileInfo:any;
  fileId:string;
  constructor(public navCtrl: NavController,public navparams:NavParams,public camera: Camera,public alertCtrl:AlertController,private myService:MyService) {
    this.fileId=navparams.data.PIC_ID;
  }

  takePhoto() {
    const options: CameraOptions = {
      quality: 100,
      allowEdit: true,
      targetWidth: 200,
      targetHeight: 200,
      saveToPhotoAlbum: true,
      sourceType:this.camera.PictureSourceType.CAMERA,//从相机选择图片
      destinationType: this.camera.DestinationType.DATA_URL
  };

  this.camera.getPicture(options).then(imageData => {
    let base64Image = {'fileType':'jpg','fileBase64':imageData};
    this.path = base64Image;
      this.avatar = imageData.slice(7);
      this.upload();
      console.log('base64Image: ' + base64Image);
    }, error => {
      console.log('Error: ' + error);
    });
  }

  chooseFromAlbum() {
    const options: CameraOptions = {
      quality: 100,
      allowEdit: true,
      targetWidth: 200,
      targetHeight: 200,
      saveToPhotoAlbum: true,
      sourceType:this.camera.PictureSourceType.SAVEDPHOTOALBUM,//从相册选择图片
      destinationType: this.camera.DestinationType.DATA_URL
    };

    this.camera.getPicture(options).then(imageData => {
      let base64Image = {'fileType':'jpg','fileBase64':imageData};
      this.path = base64Image;
      this.upload();
      console.log('base64Image: ' + base64Image);
    }, error => {
      console.log('Error: ' + error);
    }); 
  }

  presentAlert() {
      let alert = this.alertCtrl.create({title: "上传失败", message: "只能选择一张图片作为头像哦", buttons: ["确定"]});
      alert.present().then(value => {
          return value;
      });
  }
  upload(){
    this.myService.uploadFile(this.path).subscribe(data => {
      this.fileInfo = data;
      this.fileId=data.fileId;
    });
  }
  updateMyPic(){
    this.myService.updateMyPic(this.fileId).subscribe(result=>{
      if(result){
        this.alertCtrl.create({title: "上传成功", message: "头像修改成功", buttons: ["确定"]});
        this.navCtrl.push(MyPage);
      }
    });
  }
}



