import { FriendRankPage } from './../friendrank/friendrank';
///<reference path="../task/accept/accept.ts"/>
///<reference path="../task/release/release.ts"/>
///<reference path="../task/finish/finish.ts"/>
import {Component, OnDestroy, OnInit} from '@angular/core';
import {AlertController, NavController, Platform} from 'ionic-angular';
import {HomeService} from "../../providers/home.service";
import {CommonUtil} from "../../util/model/common.util";
import {UserService} from "../../providers/user.service";
import {LoginPage} from "../login/login";
import {AcceptPage} from "../task/accept/accept";
import {Geolocation} from "@ionic-native/geolocation";
import "rxjs/add/operator/filter";
import "rxjs/add/operator/takeWhile";
import {AlreadyAcceptPage} from "../task/accept/already_accept";
import {ReleaseHistoryPage} from "../task/taskList/release_history";
import {FinishHistoryPage} from "../task/taskList/finish_history";
import {TaskService} from "../../providers/task.service";
import * as echarts from 'echarts';
import {ReleasePage_2} from "../task/release/release_2";
import {UploadPage} from "../task/upload/upload";
import { GlobalVariable } from './../../app/app.global';

declare let BMap: any;
declare let baidumap_location;

@Component({
    selector: 'page-home',
    templateUrl: 'home.html'
})
export class HomePage implements OnInit, OnDestroy {
    alive: boolean;

    stations: Array<any>;
    picId: string;
    taskCount:any={};
    rate:any;//完成百分比
    showFlag:boolean=false;
    personal:any={};//个人信息
    city:any;//定位的城市

    constructor(public navCtrl: NavController,
                private geolocation: Geolocation,
                private platform: Platform,
                public alertCtrl: AlertController,
                private homeService: HomeService,
                private taskService: TaskService,
                private userService: UserService) {
        this.taskCount.num_1 = 0;
        this.taskCount.num_2 = 0;
        this.taskCount.num_3 = 0;
        // 统计查询当前任务数量
        this.getTaskCount();
    }

    ngOnInit(): void {
        this.homeService.loadScript()
            .subscribe(() => {
                // 定位
                this.locating();
                this.initPieChart();//初始化近度条
                this.updatePosition();//更新位置信息
                this.getCity();
            });
    }

    // 页面控件初始化完成，加载数据
    ionViewWillEnter(): void {
        this.alive = true;
        this.getTaskCount();
        this.updatePosition();//更新位置信息
    }

    ionViewDidLeave(): void {
        this.alive = false;
    }

    // 页面销毁
    ngOnDestroy(): void {
        this.alive = false;
    }

    loadData() {

        if (this.userService.isLogin()) {

            this.picId = "77055896-b68b-40d4-b495-83cf2602b483";

            this.homeService.getStation().subscribe(data => {
                this.stations = data.stations;
            }, CommonUtil.emptyCallback);
        } else {
            this.navCtrl.push(LoginPage);
        }

    }

    updatePosition(){//保存位置信息
        let that:any = this;
        try {
            baidumap_location.getCurrentPosition((result) => {
                let addrInfo:any = {};
                addrInfo.addr = result.addr;
                that.taskService.updatePosition(result.addr).subscribe(data => {
                }, CommonUtil.emptyCallback);
            });
        }catch(e) {
            console.error(e);
        }
    }

    toReleaseTask(){
        let that:any = this;
        if(this.personal.AUDIT_STATUS == "02"
            || this.personal.AUDIT_STATUS == ""
            || this.personal.AUDIT_STATUS == null){
            this.showAlert('审核未通过，请通过审核后再操作！');
            setTimeout(function(){//三秒后跳转到审核界面
                that.navCtrl.push(UploadPage);
            },3000);
            return;
        }
        if(this.personal.AUDIT_STATUS == "03"){//审核中
            this.showAlert('正在审核中，请审核完毕再操作!');
            return;
        }
        let taskInfo:any={};
        taskInfo.coin_personal = this.personal.COIN;
        this.navCtrl.push(ReleasePage_2,{taskInfo:taskInfo});
    }

    toAcceptTask(){
        if(this.personal.AUDIT_STATUS == "02"
            || this.personal.AUDIT_STATUS == ""
            || this.personal.AUDIT_STATUS == null){
            this.showAlert('审核未通过，请通过审核后再操作！');
            return;
        }
        if(this.personal.AUDIT_STATUS == "03"){//审核中
            this.showAlert('正在审核中，请审核完毕再操作!');
            return;
        }
        this.navCtrl.push(AcceptPage);
    }

    //显示提示信息
    showAlert(str:string) {
        let alert = this.alertCtrl.create({
            title: '',
            subTitle: str
        });
        alert.present();
    }

    toAlreadyAccept(){
        this.navCtrl.push(AlreadyAcceptPage);
    }

    //跳转到已发布的
    toReleaseHistory(){
        this.navCtrl.push(ReleaseHistoryPage);
    }

    //跳转到已完成的
    toFinish(){
        this.navCtrl.push(FinishHistoryPage);
    }
    showInfo(){
        if(this.showFlag){
            this.showFlag=false;
        }else{
            this.showFlag=true;
        }
        this.homeService.updateShowFlag(this.showFlag) .subscribe((data) => {
            console.log(data+"修改隐私状态")
        }); 
    }
    // 定位
    private locating(): void {
        let that:any = this;
        try{
            baidumap_location.getCurrentPosition((result) => {
                let point = new BMap.Point(result.lontitude, result.latitude);
                // 更新定位地点
                let _str = result.lontitude + "," + result.latitude;
                that.taskService.updatePosition({addr:_str});//更新地址
                that.city = result.city;
            });
        }catch(e) {
            console.error(e);
        }
    }

    getCity(){
        // 百度地图API功能
        let geolocation = new BMap.Geolocation();
        geolocation.getCurrentPosition(function(r){
            if(this.getStatus() == 0){
                this.city = r.address.city;
                GlobalVariable.setCity(r.address.city);//设置全局变量值
            }
            else {
                console.log('failed'+this.getStatus());
            }
        },{enableHighAccuracy: true})
    }

    // 统计查询当前任务数量
    getTaskCount(){
        this.taskService.getTaskCount().subscribe(data => {
            if(data.length > 0){
                this.taskCount = data[0];
                this.initPieChart();//重新展示进度条
            }
            if(data.length > 1){//各人金币余额和审核信息
                this.personal = data[1];
                this.showFlag=this.personal.SHOW_FLAG==1?true:false;
            }
        }, CommonUtil.emptyCallback);
    }
    goFriendRankPage(){
        this.navCtrl.push(FriendRankPage);
    }
    //进度条图形
    initPieChart(){
        let zNum = this.taskCount.num_4;//总数
        let yNum = this.taskCount.num_3;//已完成
        let rate :any;

        if(zNum == null)zNum ="0";
        if(yNum == null)yNum ="0";

        let wNum = parseInt(zNum) - parseInt(yNum);//未完成数
        if(zNum == "0"){
            rate = "0%";
            wNum = 1;
        }else{
            rate = (parseInt(yNum)/parseInt(zNum))*100;
            rate= rate.toFixed(0);
            rate = rate +"%";
        }
        this.rate = rate;
        const ec = echarts as any;
        let pie = document.getElementById("pie");
        var pieChart = ec.init(pie);

        var pieChartOption = {
            tooltip: {
                trigger: 'item',
                formatter: "{a} <br/>{b}: {c} ({d}%)"
            },
            legend: {
                orient: 'vertical',
                x: 'left',
                data:['',]
            },
            series: [
                {
                    name:'',
                    type:'pie',
                    clockWise : true,          // 默认逆时针
                    startAngle: -90,
                    minAngle: 0,
                    radius: ['60%', '72%'],
                    avoidLabelOverlap: false,
                    label: {
                        normal: {
                            show: false,
                            position: 'center'
                        },
                        emphasis: {
                            show: true,
                            textStyle: {
                                fontSize: '30',
                                fontWeight: 'bold'
                            }
                        }
                    },
                    labelLine: {
                        normal: {
                            show: false
                        }
                    },
                    data: [{name:rate, value:yNum,itemStyle:{color:'#31e0ef',
                            label : {show : false,position : 'center',fontSize:22}}},
                            {name:'未完成数',value:wNum, label:{show:false},itemStyle:{opacity:0}}
                    ]
                }
            ]
        };
        pieChart.setOption(pieChartOption);
    }

}



